// Gravity forms styles
.gform {
    &_wrapper {
        background-color: $color-white;

        h1 {
            @extend .h1;
        }

        h2 {
            @extend .h2;
        }

        h3 {
            @extend .h3;
        }

        h4 {
            @extend .h4;
        }

        h5 {
            @extend .h5;
        }

        h6 {
            @extend .h6;
        }
        
        &.gravity-theme {
            .gf_progressbar_wrapper {
                margin-bottom: 20px;

                @media (min-width:$lg) {
                    flex: 0 0 auto;
                    width: 83.33333333%;
                    margin: 0 auto 40px auto;
                }

                .gf_progressbar_title {
                    color: $title-color;
                    font-weight: $body-semibold;
                    font-size: convertRem(20);
                    margin-bottom: 14px;
                }

                .gf_progressbar {
                    background: $color-blackberry-50;
                    
                    .gf_progressbar_percentage {
                        @include gradient-progressbar($gradient-raspberry-light, $gradient-raspberry-dark);
                    }
                }
            }

            .gf_page_steps {
                .gf_step {
                    &.gf_step_active {
                        .gf_step_number  {
                            border: none;
                            @include gradient-progressbar($gradient-raspberry-light, $gradient-raspberry-dark);
                            color: $color-white;
                        }
                    }

                    &.gf_step_completed {
                        .gf_step_number  {
                            &::before {
                                background-color: $color-blackberry-500;
                                border: none;
                                height: 40px;
                                width: 40px;
                                border-radius: 50%;
                            }
                        }
                    }

                    .gf_step_number {
                        border-color: $color-blackberry-500;
                    }
                }
            }

            .gform-body {
                @media (min-width:$md) {
                    flex: 0 0 auto;
                    margin: 0 auto;
                    width: 66.66666667%;;
                }

                @media (min-width:$lg) {
                    width: 50%;
                }

                @media (min-width:$xl) {
                    flex: 0 0 auto;
                    width: 33.33333333%;
                }
            }

            input[type='date'],
            input[type='datetime-local'],
            input[type='email'],
            input[type='file'],
            input[type='month'],
            input[type='number'],
            input[type='password'],
            input[type='range'],
            input[type='search'],
            input[type='tel'],
            input[type='text'],
            input[type='time'],
            input[type='url'],
            input[type='week'],
            textarea,
            select,
            .select2-css.select2-selection--multiple,
            .select2-css.select2-selection--single {
                line-height: 1.2;
                padding: 12px 18px;

                &:focus {
                    border-color: $form-border-focus;
                }
            }

            .gfield {
                &_label {
                    color: $color-black;
                    font-weight: $body-semibold;
                    line-height: 1.11;
                }

                &_required,
                &_required.gfield_required_custom {
                    color: $body-color;
                    font-style: normal;
                    font-size: convertRem(16);
                    text-transform: lowercase;
                }

            }

            .ginput_container {
                margin-top: 5px;

                &.ginput_complex {
                    .gform-grid-col {
                        input {
                            margin-bottom: 10px;
                        }
                    }
                }

                &.ginput_container_checkbox {
                    .gchoice {
                        background-color: $color-blackberry-50;
                        border-radius: 16px;
                        color: $form-text;
                        cursor: pointer;
                        display: block;
                        line-height: 1.2;
                        margin: 0 0 16px 0;
                        padding: 12px 45px 12px 18px;
                        position: relative;
                        cursor: pointer;
                        position: relative;
                    
                        &::before {
                            content: '';
                            border: 1px solid $form-border;
                            border-radius: 4px;
                            background: $color-white;
                            width: 24px;
                            height: 24px;
                            position: absolute;
                            top: 50%;
                            transform: translateY(-50%);
                            right: 18px;
                            color: transparent;
                            transition: 0.3s ease all;
                            pointer-events: none;
                        }
                    
                        &::after {
                            @extend %icon;
                            content: '\e95c';
                            font-size: 19px;
                            color: $color-raspberry-500;
                            position: absolute;
                            top: 50%;
                            transform: translateY(-50%);
                            right: 21px;
                            opacity: 0;
                            transition: 0.3s ease all;
                            pointer-events: none;
                        }
                    
                        &.__checked {
                            &::before {
                                border-color: $color-raspberry-500;
                            }
                    
                            &::after {
                                opacity: 1;
                            }
                        }

                        &.__focused {
                            &::before {
                                border-color: darken($color-raspberry-500, 10%);
                            }
                        }
                    
                        input[type='checkbox'] {
                            position: absolute;
                            margin: 0;
                            top: 50%;
                            transform: translateY(-50%);
                            right: 18px;
                            width: 24px; // match width and height of psuedo elements
                            height: 24px; // match width and height of psuedo elements
                            opacity: 0 !important;
                        }
                    }
                }

                &.ginput_container_radio {
                    .gchoice {
                        position: relative;
                        background-color: $color-blackberry-50;
                        border-radius: 16px;
                        color: $form-text;
                        cursor: pointer;
                        display: block;
                        line-height: 1.2;
                        margin-bottom: 16px;
                        padding: 12px 18px;

                        input[type='radio'] {
                            position: absolute;
                            margin: 0;
                            right: 18px;
                            width: 24px; // match width and height of psuedo elements
                            height: 24px; // match width and height of psuedo elements
                            opacity: 0 !important;
                        }
                    
                        label {
                            position: relative;
                            display: block;
                            cursor: pointer;
                            color: $form-text;
                            display: flex;
                            flex-direction: row-reverse;
                            align-items: center;
                            justify-content: space-between;
                            max-width: 100%;

                            &::before,
                            &::after {
                                display: block;
                                width: 24px;
                                height: 24px;
                            }
                        
                            &:before {
                                content: "";
                                border: 1px solid $form-border;
                                border-radius: 50%;
                                background: $color-white;
                                position: relative;
                                color: transparent;
                                transform: none;
                            }
                        
                            &::after {
                                transform: scale(0.4);
                                content: "";
                                background-color: $color-raspberry-500;
                                opacity: 0;
                                border-radius: 50%;
                                position: absolute;
                                right: 1px;
                                color: transparent;
                                transition: 0.3s ease all;
                            }
                        }

                        &.__checked {
                            label {
                                &::before {
                                    border-color: $color-raspberry-500;
                                }

                                &::after {
                                    opacity: 1;
                                }
                            }
                        }
                    }
                }

                &.ginput_container_select,
                &.ginput_complex {
                    .ginput_container,
                    input {
                        margin-top: 0 !important;
                        margin-bottom: 0 !important;
                    }

                    select {
                        padding: 10px 18px;
                    }

                    .select2 .select2-selection--single {
                        width: 100%;
                    }
                }

                &.ginput_container_multiselect {
                    margin-bottom: 20px;
                    position: relative;

                    .select2 .select2-selection--multiple  {
                        @media (min-width:$lg) {
                            height: 160px;
                        }

                        .select2-selection__rendered {
                            flex-wrap: wrap;
                            row-gap: 10px;
                        }
                    }

                    .select2-container--default .select2-selection--multiple .select2-selection__choice {
                        background: rgba($color-raspberry-900, 0.2);
                        border: none;
                        border-radius: 30px;
                        font-weight: $body-regular;
                        line-height: 1.2;
                        display: flex;
                        align-items: center;
                        column-gap: 5px;
                        padding: 15px 30px;

                        .select2-selection__choice__remove {
                            border: none;
                            color: $color-raspberry-900;
                            font-size: convertRem(24);
                            font-weight: $body-regular;
                            height: 100%;
                            margin-right: 8px;
                            left: 15px;
                        }

                        .select2-selection__choice__display {
                            padding-left: 10px;
                        }
                    }
                }

                &.ginput_container_time {
                    max-width: 80px;
                    min-width: 80px;
                }

                &.ginput_container_consent {
                    color: $form-text;
                    cursor: pointer;
                    display: block;
                    margin: 5px 0 20px 0;
                    min-height: 20px;
                    padding-left: 45px;
                    position: relative;
                    cursor: pointer;

                    &::before {
                        content: '';
                        border: 1px solid $form-border;
                        border-radius: 4px;
                        background: $form-bg;
                        width: 24px;
                        height: 24px;
                        position: absolute;
                        top: -1px;
                        left: 0;
                        color: transparent;
                        transition: 0.3s ease all;
                        pointer-events: none;
                    }
                
                    &::after {
                        @extend %icon;
                        content: '\e95c';
                        font-size: 19px;
                        color: $color-raspberry-900;
                        position: absolute;
                        top: 2px;
                        left: 4px;
                        opacity: 0;
                        transition: 0.3s ease all;
                        pointer-events: none;
                    }

                    &.__checked {
                        &::before {
                            background: #DBE7E8;
                            border-color: $color-raspberry-900;
                        }

                        &::after {
                            opacity: 1;
                        }

                    }

                    input[type='checkbox'] {
                        position: absolute;
                        margin: 0;
                        top: 0;
                        left: 0;
                        width: 24px; // match width and height of psuedo elements
                        height: 24px; // match width and height of psuedo elements
                        opacity: 0 !important;
                    }
                }
            }

            .gform_heading {
                .gform_title {
                    @extend .h2;
                }
            }

            .gform_page_footer {
                .button {
                    font-weight: $body-regular;
                    @extend .__dark;

                    &.gform-theme-button--secondary {
                        @extend .__gradient-dark;
                    }
                }
            }
            
            .gfield_error [aria-invalid=true] { 
                border-color: $color-error-80;
            }

            .validation_message {
                border-color: $color-error-80;
                border-radius: 16px;
                color: $color-error-80;
            }

            .gform_validation_errors {
                border-color: $color-error-80;
                margin-bottom: 40px;

                &:focus-visible {
                    outline-color: $color-error-80;
                }

                h1,
                h2,
                h3,
                p,
                ol li,
                ul li,
                a {
                    color: $color-error-80 !important;
                }

                h2 {
                    font-size: convertRem(18);
                }

                a {
                    text-decoration: underline;
                    transition: 0.3s ease all;

                    &:hover,
                    &:focus {
                        text-decoration: none;
                    }
                }
            }
        }
    }
}