// Grid containers
$container-max-widths: (
    sm: 100%,
    md: 720px,
    lg: 960px,
    xl: 1232px,
    xxl: 1400px
);

@function container-max-widths($key: "md") {
    @return map-get($container-max-widths, $key);
}

.container {
    --bs-gutter-x: 16px !important;
    padding-right: calc(var(--bs-gutter-x) * 1) !important;
    padding-left: calc(var(--bs-gutter-x) * 1) !important;

    @media (min-width:1600px) {
        padding-right: calc(var(--bs-gutter-x) * .6) !important;
        padding-left: calc(var(--bs-gutter-x) * .6) !important;
    }

    &-fluid {
        max-width: 1920px;
    }
}

.row>* {
    @media (min-width:720px) {
        padding-right: calc(var(--bs-gutter-x) * .4) !important;
        padding-left: calc(var(--bs-gutter-x) * .4) !important;
    }
}

:root {
    --bs-gutter-x: 16px
    --bs-gutter-y: 16px
}

[class*="col-"] {
    position: relative;
}