.paging {
    padding: 20px 0;
    display: flex;
    font-size: convertRem(18);

    @media (min-width:$md) {
        padding: 62px 0 68px 0;
    }

    &-list {
        display: flex;
        position: relative;

        @media (min-width:$md) {
            &::before {
                content: '';
                background-color: $color-blackberry-100;
                border-radius: 16px;
                position: absolute;
                left: 68px;
                top: -4px;
                height: calc(100% + 8px); //plus two times value of top
                width: calc(100% - 136px); //minus two times value of left
            }
        }
    }

    &-item {
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: convertRem(20);
        font-weight: $body-semibold;
        font-variant-numeric: lining-nums proportional-nums;
        padding: 13px 22px;
        margin: 0 5px;
        background: $color-clear;
        color: $title-color;
        transition: all 0.3s;
        position: relative;
        border-radius: 16px;

        &:hover,
        &:focus {
            background: $color-blackberry-50;
        }

        &.__disabled {
            cursor: not-allowed;
            opacity: 0.6;
        }

        &.__current {
            background: $color-white;
        }

        &.__previous,
        &.__next {
            background-color: $color-blackberry-500;
            font-size: 0;
            width: 52px;
            height: 50px;

            &::before {
                @extend %icon;
                content: '\e919';
                color: $color-white;
                font-size: 20px;
            }

            &:hover,
            &:focus {
                background-color: $color-blackberry-400;
            }
        }

        &.__previous {
            margin-right: 16px;
        }

        &.__next {
            margin-left: 16px;

            &::before {
                content: '\e91a';
            }
        }
    }

    li:not(:first-child):not(.active):not(:last-child) {
        display: none;

        @media (min-width: $md) {
            display: inherit;
        }
    }
}