.post {
    &-image {
        border-radius: 16px 16px 0 0;
        overflow: hidden;
        @include aspect(1.69, 1);

        img {
            transition: 0.3s ease all;
        }

        &-placeholder {
            background-color: $color-blackberry-500;
            border-radius: 16px 16px 0 0;
            @include aspect(1.69, 1);
        }
    }

    &-content {
        @media (min-width:$md) {
            padding-right: 25px;
        }

        &-wrap {
            background-color: $color-white;
            border-radius: 0 0 16px 16px;
            padding: 32px 18px;
        }

        .button {
            padding: 24px 0 0 0;
        }
    }

    &-title {
        margin-bottom: 9px;
    }

    &-item {
        &:hover,
        &:focus {
            @extend %inline-button-hover;
            @extend %image-zoom;
        }
        
        &-wrap {
            height: 100%;
            background-color: $color-white;
            border-radius: 30px;
        }
    }
}