form {
    width: 100%;
    a {
        @extend .a;
        text-decoration: underline;

        &:hover,
        &:focus,
        &:active {
            text-decoration: none;
        }
    }
}

/* Input types */
input[type='date'],
input[type='datetime-local'],
input[type='email'],
input[type='file'],
input[type='month'],
input[type='number'],
input[type='password'],
input[type='range'],
input[type='search'],
input[type='tel'],
input[type='text'],
input[type='time'],
input[type='url'],
input[type='week'],
textarea,
select,
.select2-css.select2-selection--multiple,
.select2-css.select2-selection--single {
    color: $form-text;
    background: $form-bg;
    font-family: $body-family;
    font-size: $font-size;
    line-height: 1.2;
    border: 1px solid $form-border;
    border-radius: 16px;
    height: auto;
    width: 100%;
    margin: 0;
    padding: 12px 18px;
    transition: all 200ms;
    box-sizing: border-box;

    &:focus {
        border-color: $form-border-focus;
    }
}

input[type='file'] {
    padding: 10px;
    border: 1px dashed $form-border;
}

textarea {
    min-height: 180px;
}

.field_wrap {
    position: relative;
    margin: 0 0 20px 0;

    .label_wrap {
        padding-bottom: 6px;

        &,
        label {
            font-weight: $body-bold;
            color: $form-text;
        }
    }
    .helper_wrap {
        color: $form-helper;
        font-size: 0.8rem;
        padding-bottom: 6px;
        line-height: 1.3;

        &-icon {
            width: 14px;
            height: 14px;
            font-size: 10px;
            line-height: 1.1;
            font-weight: $body-bold;
            display: inline-block;
            text-align: center;
            vertical-align: middle;
            background-color: $form-helper;
            color: white;
            border-radius: 100px;
            transform: translateY(-1px);
            margin-right: 4px;
        }
    }
}

/* Flatpickr */
.field_wrap.__date,
.field_wrap.__time,
.field_wrap.__datetime {
    .input_wrap {
        position: relative;
        &::before {
            @extend %icon;
            content:'\e916';
            color: $form-text;
            height: 100%;
            position: absolute;
            right: 15px;
            top: 50%;
            transform: translateY(-50%);
            width: 20px;
            pointer-events: none;
            display: flex;
            align-items: center;
            font-size: 18px;
        }
    }
}
.flatpickr-time .numInputWrapper input {
    padding: 9px;
    border: none;
}

/* Checkboxes */
.field_wrap.__checkbox {
    input[type='checkbox'] {
        position: absolute;
        margin: 0;
        top: 0;
        left: 0;
        width: 20px; // match width and height of psuedo elements
        height: 20px; // match width and height of psuedo elements
        opacity: 0 !important;
    }

    >label,
    >.input_wrap>label {
        color: $form-text;
        cursor: pointer;
        display: block;
        margin: 0 0 15px 0;
        min-height: 20px;
        padding-left: 30px;
        position: relative;

        &::before {
            content: '';
            border: 1px solid $form-border;
            background: $form-bg;
            width: 20px;
            height: 20px;
            position: absolute;
            top: -1px;
            left: 0;
            color: transparent;
            transition: 0.3s ease all;
        }

        &::after {
            @extend %icon;
            content: '\e95c';
            font-size: 14px;
            font-weight: 900;
            color: white;
            position: absolute;
            top: 2px;
            left: 4px;
            opacity: 0;
            transition: 0.3s ease all;
        }

        &.__checked {
            &::before {
                background: $color-raspberry-800;
                border-color: $color-raspberry-800;
            }

            &::after {
                opacity: 1;
            }

            &.__focused {
                &::before {
                    background: darken($color-raspberry-800, 20%);
                    border-color: darken($color-raspberry-800, 20%);
                }
            }
        }

        &.__focused {
            &::before {
                border-color: darken($color-raspberry-800, 20%);
                background: $form-bg;
            }
        }
    }
}

/* Radiobuttons */
.field_wrap.__radio {
    input[type='radio'] {
        position: absolute;
        margin: 0;
        top: 0;
        left: 0;
        width: 20px; // match width and height of psuedo elements
        height: 20px; // match width and height of psuedo elements
        opacity: 0 !important;
    }

    >label,
    >.input_wrap>label {
        position: relative;
        display: block;
        margin: 0 0 15px 0;
        padding-left: 30px;
        cursor: pointer;
        color: $form-text;

        &::before {
            content: '';
            border: 1px solid $form-border;
            background: $form-bg;
            color: transparent;
            border-radius: 50%;
            width: 20px;
            height: 20px;
            position: absolute;
            top: -1px;
            left: 0;
            transition: 0.3s ease all;
        }

        &::after {
            content: '';
            border: 1px solid white;
            background: white;
            color: transparent;
            border-radius: 50%;
            transform: scale(0.4);
            width: 20px;
            height: 20px;
            position: absolute;
            top: -1px;
            left: 0;
            opacity: 0;
            transition: 0.3s ease all;
        }

        &.__checked {
            &::before {
                background: $color-raspberry-800;
                border-color: $color-raspberry-800;
            }

            &::after {
                opacity: 1;
            }

            &.__focused {
                &::before {
                    background: darken($color-raspberry-800, 20%);
                    border-color: darken($color-raspberry-800, 20%);
                }
            }
        }

        &.__focused {
            &::before {
                border-color: darken($color-raspberry-800, 20%);
                background: $form-bg;
            }
        }
    }
}

/* Range */
.field_wrap.__range {
    .input_wrap {
        $range-bg: $color-raspberry-800;
        $range-thumb-stroke: black;
        $range-thumb-bg: white;

        input[type=range] {
        	width: 100%;
        	background-color: transparent;
        	-webkit-appearance: none;
            display: block;
        	&:focus {
        		&::-webkit-slider-runnable-track {
        			background: $range-bg;
        		}
        		&::-ms-fill-lower {
        			background: $range-bg;
        		}
        		&::-ms-fill-upper {
        			background: $range-bg;
        		}
        	}
        	&::-webkit-slider-runnable-track {
        		background: $range-bg;
        		border: 0;
        		width: 100%;
        		height: 5px;
        		cursor: pointer;
        	}
        	&::-webkit-slider-thumb {
        		width: 20px;
        		height: 20px;
        		background: $range-thumb-bg;
        		border: 1px solid $range-thumb-stroke;
        		border-radius: 50px;
        		cursor: pointer;
        		-webkit-appearance: none;
        	}
        	&::-moz-range-track {
        		background: $range-bg;
        		border: 0;
        		width: 100%;
        		height: 5px;
        		cursor: pointer;
        	}
        	&::-moz-range-thumb {
        		width: 20px;
        		height: 20px;
        		background: $range-thumb-bg;
        		border: 1px solid $range-thumb-stroke;
        		border-radius: 50px;
        		cursor: pointer;
        	}
        	&::-ms-track {
        		background: transparent;
        		border-color: transparent;
        		border-width: 8.5px 0;
        		color: transparent;
        		width: 100%;
        		height: 5px;
        		cursor: pointer;
        	}
        	&::-ms-fill-lower {
        		background: #2f6ea5;
        		border: 0;
        	}
        	&::-ms-fill-upper {
        		background: $range-bg;
        		border: 0;
        	}
        	&::-ms-thumb {
        		width: 20px;
        		height: 20px;
        		background: $range-thumb-bg;
        		border: 1px solid $range-thumb-stroke;
        		border-radius: 50px;
        		cursor: pointer;
        		margin-top: 0px;
        	}
        }
        @supports (-ms-ime-align:auto) {
    		input[type=range] {
                margin: 0;
            }
        }
    }
}

/* Placeholders */
::-webkit-input-placeholder,
::placeholder {
    opacity: 1;
    color: $form-placeholder;
}

/* Error styling */
// Input error styles
.field_wrap.error {
    label {
        color: $form-error-text;
    }

    input,
    select,
    textarea,
    .select2-css {
        border-color: $form-error-border;
    }

    // Error styles when inline error message present
    &.__inline {
        input,
        select,
        textarea,
        .select2-css {
            border-bottom-left-radius: 0;
            border-bottom-right-radius: 0;
        }
    }
}

// Inline error message
.input_error,
.field-validation-error {
    background: $form-error-bg;
    border: 1px solid $form-error-border;
    border-top: none;
    color: $form-error-text;
    padding: 15px 20px 14px 20px;
    display: flex;
    align-items: center;

    &::before {
        @extend %icon;
        content: '\e968';
        color: $form-error-text;
        font-size: 12px;
        margin-right: 12px;
    }
}

// success feedback
.success_feedback {
    margin: 0 0 2em 0;
    background: white;
    padding: 15px 20px;
    border: 1px solid $form-success-border;
    background: $form-success-bg;
    color: $form-success-text;
    text-align: left;

    ul {
        margin: 0;
        padding: 0;
        list-style: none;

        li {
            margin-bottom: 5px;
            display: flex;
            align-items: center;

            &::before {
                @extend %icon;
                content: '\e95c';
                color: $form-success-text;
                font-size: 12px;
                margin-right: 12px;
            }

            &:last-child {
                margin: 0;
            }
        }
    }
}

// Error feedback
.error_feedback,
.validation-summary-errors {
    margin: 0 0 2em 0;
    background: white;
    padding: 15px 20px;
    border: 1px solid $form-error-border;
    background: $form-error-bg;
    color: $form-error-text;
    text-align: left;

    ul {
        margin: 0;
        padding: 0;
        list-style: none;

        li {
            margin-bottom: 5px;
            display: flex;
            align-items: center;

            &::before {
                @extend %icon;
                content: '\e968';
                color: $form-error-text;
                font-size: 12px;
                margin-right: 12px;
            }

            &:last-child {
                margin: 0;
            }
        }
    }
}

// Error styles for Checkboxes
.field_wrap.__checkbox {
    &.error {
        legend {
            color: $form-error-text;
        }

        label {
            color: $form-error-text;

            &::before {
                background: $form-error-bg;
                border: 1px solid $form-error-border;
            }

            &.__checked {
                &::before {
                    background: $form-error-border;
                    border-color: $form-error-border;
                }

                &::after {
                    color: white;
                }

                &.__focused {
                    &::before {
                        background: darken($form-error-border, 20%);
                        border: 1px solid darken($form-error-border, 20%);
                    }
                }
            }

            &.__focused {
                &::before {
                    border-color: darken($form-error-border, 10%);
                    background: darken($form-error-bg, 5%);
                }
            }
        }
    }
    .input_error {
        border-top: 1px solid $form-error-border;
    }
}

// Error styles Radiobuttons
.field_wrap.__radio {
    &.error {
        legend {
            color: $form-error-text;
        }
        label {
            color: $form-error-text;

            &::before {
                background: $form-error-bg;
                border: 1px solid $form-error-border;
            }
            &::after {
                color: $form-error-border;
            }

            &.__checked {
                &::before {
                    background: $form-error-border;
                    border-color: $form-error-border;
                }

                &.__focused {
                    &::before {
                        background: darken($form-error-border, 20%);
                        border: 1px solid darken($form-error-border, 20%);
                    }
                }
            }

            &.__focused {
                &::before {
                    border-color: darken($form-error-border, 10%);
                    background: darken($form-error-bg, 5%);
                }
            }
        }
    }
    .input_error {
        border-top: 1px solid $form-error-border;
    }
}

// Error styles for File upload
.field_wrap.__file {
    &.error {
        .input_error {
            border-top: 1px solid $form-error-border;
            margin-top: -1px;
            position: relative;
        }
    }
}


/* Select2 */
// Main select styles
.select2-css {
    &.select2-selection--multiple,
    &.select2-selection--single {
        font-size: convertRem(16);
        width: 100%;
        padding: 3px 18px;

        .select2-selection__arrow {
            top: 50%;
            transform: translateY(-50%);
            right: 20px;
        }
    }

    // Styles for multiselect
    &.select2-selection--multiple {
        .select2-selection__rendered {
            display: flex;
            align-items: center;
        }
        // Styles for single chosen options
        .select2-selection__choice {
            padding: 10px;
            margin: 0 10px 0 0;
        }
    }
}

.select2-css.select2-selection--single {
    .select2-selection__rendered {
        padding-left: 0;
    }
}

.select2-template {
    &-image {
        display: flex;
        align-items: center;

        img {
            margin: 0 10px 0 0;
        }
    }
}

// Styles for open and focus
.select2-container--focus,
.select2-container--open {
    .select2-css {
        &.select2-selection--multiple,
        &.select2-selection--single {
            border-color: $form-border-focus;
        }
    }
    .select2-dropdown {
        border-color: $form-border-focus;      
        overflow: hidden;

        &.select2-dropdown--above {
            border-top-right-radius: 18px;
            border-top-left-radius: 18px;
        }

        &.select2-dropdown--below {
            border-bottom-right-radius: 18px;
            border-bottom-left-radius: 18px;
        }
    }
}

// Styles for dropdown
.select2-results .select2-results__option {
    font-size: convertRem(16);
    padding: 5px 18px;

    // Styles for highlight
    &--highlighted[aria-selected] {
      background: $color-blackberry-800;
    }
}

// Styles for search field
.select2-search--dropdown .select2-search__field {
    padding: 15px 20px;
}

//error styling for select2 for wordpress
select.error + .select2.select2-container {
    .select2-css {
        &.select2-selection--single {
            border: 1px solid $form-error-border;
        }
    }
}

// Reset button
.__reset {
    margin-bottom: 20px;

    &:active {
        transform: scale(105%);
    }
}

.__cleared {
    min-height: 16px; // prevents resizing of select filters on form clear
}
