/* Headings */
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
    color: $title-color;
    font-family: $title-font;
    font-weight: $title-extra-bold;
    margin: 0 0 16px 0;
    line-height: 0.75;
    text-transform: uppercase;
    font-variant-numeric: lining-nums proportional-nums;
    letter-spacing: -1.2px;

    @media (min-width:$lg) {
        line-height: 0.7;
    }
}

.h1 {
    font-size: convertRem(40);
    letter-spacing: 0.3px;

    @media (min-width:$lg) {
        font-size: convertRem(88);
        line-height: 0.69;
        letter-spacing: normal;
    }
}

.h2 {
    font-size: convertRem(34);
    line-height: 0.75;
    letter-spacing: 0.3px;

    @media (min-width:$lg) {
        font-size: convertRem(56);
        line-height: 0.75;
        letter-spacing: normal;
    }
}

.h3 {
    font-size: convertRem(28);
    letter-spacing: 0.3px;

    @media (min-width:$lg) {
        font-size: convertRem(40);
        letter-spacing: normal;
    }
}

.h4 {
    font-size: convertRem(24);
    letter-spacing: 0.3px;

    @media (min-width:$lg) {
        font-size: convertRem(32);
        line-height: 0.81;
    }
}

.h5 {
    font-size: convertRem(20);
    letter-spacing: 0.3px;

    @media (min-width:$md) {
        font-size: convertRem(24);
    }
}

.h6 {
    font-size: convertRem(18);
    letter-spacing: 0.3px;

    @media (min-width:$lg) {
        font-size: convertRem(22);
    }
}

.a:not(.button) {
    color: $color-blueberry-600;
    display: inline;
    text-decoration: underline;

    &:hover,
    &:focus {
        color: darken($color-blueberry-600, 15%);
        text-decoration: none;
    }
}

/* Usercontent */
.usercontent,
.mce-content-body {
    h1 {
        @extend .h1;
    }

    h2 {
        @extend .h2;
    }

    h3 {
        @extend .h3;
    }

    h4 {
        @extend .h4;
    }

    h5 {
        @extend .h5;
    }

    h6 {
        @extend .h6;
    }

    p {
        font-size: convertRem(20);
        margin: 0 0 16px 0;
        line-height: 1.2;
    }

    a {
        @extend .a;
    }

    ul,
    ol {
        margin: 0 0 2.5rem 0;
        padding: 0;
        list-style-type: none;
        line-height: 1.6em;
    }

    ul {
        >li {
            position: relative;
            margin: 0 0 16px 0;
            padding: 0 0 0 20px;

            &::before {
                content: '';
                width: 8px;
                height: 8px;
                border-radius: 50%;
                background: $color-blackberry-500;
                position: absolute;
                top: 12px;
                left: 0;
            }

            >ol,
            >ul {
                padding: 16px 0 0 0;
            }
        }
    }

    ol {
        counter-reset: item;

        >li {
            position: relative;
            margin: 0 0 16px 0;
            padding: 0 0 0 38px;
            counter-increment: item;

            &::before {
                content: counter(item, decimal-leading-zero) " ";
                background: none;
                display: inline-block;
                color: $color-blackberry-500;
                font-family: $title-font;
                font-size: convertRem(24);
                font-variant-numeric: lining-nums proportional-nums;
                font-weight: $title-extra-bold;
                width: auto;
                position: absolute;
                top: 0;
                left: 0;
            }

            >ol,
            >ul {
                padding: 16px 0 0 0;
            }

            >ol>li {
                padding: 0 0 0 40px;

                >ol>li {
                    padding: 0 0 0 50px;
                }
            }
        }
    }

    figure {
        blockquote {
            margin-bottom: 0;
        }

        figcaption {
            color: $color-blackberry-500;
            padding: 18px 0 40px 20px;

            @media (min-width:$lg) {
                padding-left: 30px;
            }
        }
    }

    blockquote {
        @extend .h3;
        word-break: normal;
        white-space: normal;
        background: none;
        letter-spacing: -1.2px;
        margin: 24px 0 38px 0;
        padding: 0 0 0 16px;

        @media (min-width:$xl) {
            margin-right: 165px;
        }

        &::before {
            @extend %icon;
            content: '\e908';
            color: $title-color;
            font-size: convertRem(20);
            position: relative;
            left: -16px;
            margin-bottom: 18px;

            @media (min-width:$lg) {
                font-size: convertRem(30);
            }
        }

        >p {
            color: $color-blackberry-500;
            line-height: 0.75;
            margin: 0 0 18px 0;

            @media (min-width:$lg) {
                line-height: 0.7;
            }
        }

        footer {
            padding-left: 20px;
        }
    }

    .iframe-wrap {
        position: relative;
        padding-bottom: 56.25%;
        padding-top: 30px;
        height: 100%;
        overflow: hidden;

        iframe,
        object,
        embed {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
        }
    }

    .table-wrap {
        border-radius: 16px;
        display: block;
        width: 100%;
        overflow-x: auto;

        table {
            width: 100%;
            max-width: 100%;
        }
    }

    table {
        border-collapse: collapse;

        tr {
            td,
            th {
                padding: 16px;
                text-align: left;

                &:not(:last-child) {
                    border-right: 1px solid $color-white;
                }
            }

            th {
                background: $color-blackberry-400;
                color: $color-white;
                padding: 30px 16px;
            }
        }

        tbody {
            tr:nth-child(odd) {
                background-color: $color-white;
            }

            tr:nth-child(even) {
                background-color: $color-blackberry-100;
            }
        }
    }

    :last-child {
        margin-bottom: 0;
    }

    // wp wysiwyg styles
    .alignleft {
        float: left;
    }

    .alignright {
        float: right;
    }
}

.__background-dark {
    background-color: $color-blackberry-500;

    .usercontent {

        p {
            color: $color-blackberry-50;
        }

        a:not(.button) {
            color: $color-blackberry-100;
        }

        h1,
        h2,
        h3,
        h4,
        h5,
        h6 {
            color: $color-white;
        }

        ul, 
        ol {
            li {
                color: $color-blackberry-50;
            }
        }

        ul li::before {
            background-color: $color-blackberry-50;
        }

        ol li::before {
            color: $color-blackberry-50;
        }
    }

    .accordion-content .usercontent {

        p,
        a:not(.button),
        h1,
        h2,
        h3,
        h4,
        h5,
        h6 {
            color: $title-color;
        }

        ul, 
        ol {
            li {
                color: $title-color;
            }
        }

        ul li::before {
            background-color: $title-color;
        }

        ol li::before {
            color: $title-color;
        }
    }
}

.__background-light {
    background-color: $color-white;
}

.textblock:has(.gform_wrapper) {
    background-color: $color-white;
    border-radius: 40px;
    padding: 40px 0;
    margin-bottom: 60px;

    @media (min-width:$lg) {
        padding: 80px 0;
        margin-bottom: 119px;
    }

    .col-lg-10,
    .col-xl-8 {
        width: 100%;
    }
}

.textblock.__left-align {
    .usercontent {
        @media (min-width:$xl) {
            padding-right: 95px;
        }
    }
}

.textblock-cta {
    margin-top: 24px;

    &.__button-center {
        display: flex;
        justify-content: center;
    }

    &.__button-right {
        display: flex;
        justify-content: flex-end;
    }
}