/* Burger */
.burger {
    display: inline-block;
    width: 73px;
    height: 85px;
    font-size: 0;
    color: $color-white;
    z-index: 9999;
    position: absolute;
    top: -14px;
    right: 0;
    cursor: pointer;
    transition: all 0.5s;

    &.__active {
        .burger {
            &-toggle {
                background: transparent;

                &:before {
                    top: 0;
                    background: $color-white;
                    transform: rotate(135deg);
                }

                &:after {
                    bottom: 0;
                    background: $color-white;
                    transform: rotate(225deg);
                }
            }
        }
    }

    @media (min-width: $nav-bp) {
        display: none;
    }

    &-toggle {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 27px;
        height: 3px;
        background: $color-white;

        &:before,
        &:after {
            content: '';
            position: absolute;
            left: 0;
            width: 100%;
            height: 100%;
            background: $color-white;
            transform: translateZ(0);
            backface-visibility: hidden;
            transition: transform 0.3s;
        }

        &:before {
            top: -8px;
            transform: rotate(0);
        }

        &:after {
            bottom: -8px;
            transform: rotate(0);
        }
    }
}

.navcontainer {
    display: flex;
    height: 100%;
    justify-content: flex-end;

    @media (min-width:$lg) {
        flex-wrap: wrap;
        row-gap: 12px;
    }

    @media (min-width:$xl) {
        flex-wrap: nowrap;
    }
}

/* Navwrap for mobile positioning */
.navwrap {
    position: fixed;
    top: 0;
    right: -$nav-width-mobile;
    bottom: 0;
    height: 100%;
    width: $nav-width-mobile;
    z-index: 8888;
    overflow: hidden;
    transition: all 0.5s;
    padding-top: 86px; //height of header

    &.__active {
        right: 0 !important;
    }

    @media (min-width: $nav-bp) {
        position: inherit;
        top: inherit;
        right: inherit;
        bottom: inherit;
        height: auto;
        width: 100%;
        z-index: 9999;
        overflow: inherit;
        transition: all 0.3s;
        display: flex;
        justify-content: flex-end;
        padding: 0;
    }

    .nav-aux {
        display: block;
        @media (min-width: $nav-bp) {
            display: none;
        }
    }
}

/* Nav */
%mobile-nav-link {
    display: block;
    font-size: convertRem(20);
    color: $color-white;
    padding: 8px 16px;
    position: relative;
    transition: all 0.3s;   
    width: 100%;
    text-align: left;
    margin-bottom: 16px;

    @media (min-width:$nav-bp) {
        margin-bottom: 0;
    }
}

%nav-link-expand-button {
    @media (min-width: $nav-bp) {
        bottom: 0;
        transition: all 300ms ease;
        display: block;
        width: auto;
        position: absolute;
        top: 0px;
        left: auto;
        right: 0px;
        padding: 8px 16px;
        height: 100%;
        color: $color-white;
        transform: translateX(0%);
    }

    &::before {
        transform: translate(0);
        transition: 0.3s ease all;
    }

    &:after {
        @media (min-width: $nav-bp) {
            font-family: icomoon;
            color: $title-color;
            content: '\e91a';
            background: $color-blackberry-400;
            border-radius: 50%;
            transition: all 300ms ease;
            transform: translate(-50%, -50%);
            position: absolute;
            top: 50%;
            left: 50%;
            color: $color-white;
            width: 16px;
            height: 16px;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }

    @media (min-width:$nav-bp) {
        &:hover,
        &:focus,
        &.__active {
            &::before {
                transform: translateX(4.5px);
            }

            &::after {
                transform: translate(calc(-50% + 5px), -50%);
            }
        }
    }
}

.nav {
    height: 100%;
    width: 100%;
    overflow-y: scroll;
    background: $color-blackberry-500;
    transition: all 0.3s;
    padding: 26px 16px;

    @media (min-width: $nav-bp) {
        overflow-y: inherit;
        background: none;
        width: auto;
        padding: 0;
        opacity: 1;
    }

    > ul {
        align-items: center;

        @media (min-width: $nav-bp) {
            height: 100%;
        }
        
        > li.menu-item {
            @media (min-width: $nav-bp) {
                height: auto;
            }

            &.__link-only {
                @media (min-width:$nav-bp) {
                    a {
                        padding-right: 0;

                        .link-text {
                            transform: translateX(-16px);
                            transition: 0.3s ease all;
                        }
            
                        &::before {
                            content: '';
                            background-color: $color-white;
                            width: 8px;
                            height: 8px;
                            border-radius: 50%;
                            margin-right: 8px;
                            opacity: 0;
                            position: relative;
                            transform: translateY(10px);
                            left: 0;
                            transition: 0.3s ease all;
                        }
            
                        &:hover,
                        &:focus {
                            &::before {
                                opacity: 1;
                            }
            
                            .link-text {
                                transform: translateX(0)
                            }
                        }
                    }
                }
            }

            button {
                font-size: 0;
            }

            > a {
                display: flex;
                align-content: center;
                flex-wrap: wrap;
                font-weight: $body-semibold;
                white-space: nowrap;
            } 
        }

        > li.menu-item-has-children {
            button{
                &::before {
                    @extend %icon;
                    content: '\e918';
                    color: $color-white;
                    font-size: convertRem(16);
                    width: 16px;
                    height: 26px;
                    position: relative;
                    z-index: 1;
                }
            }

            a {
                display: inline-block;
                width: fit-content;
                max-width: 75%;
            }

            @media (min-width:$nav-bp) {
                

                button {
                    position: absolute;
                    height: 100%;
                    left: 0;
                    width: auto;
                    padding: 0;
                    transition: 0.3s ease all;

                    &::before {
                        height: auto;
                        transform: translateY(2px);
                    }

                    &:hover,
                    &:focus,
                    &:active {
                        &::before {
                            transform: translate(5px, 2px);
                        }
                    }
                }

                a {
                    display: inline-block;
                    margin-left: 16px;

                    &:hover,
                    &:focus,
                    &:active {
                        color: $color-blackberry-100;
                    }
                }
            }
        }
    }

    ul {
        float: left;
        width: 100%;
        transition: 0.3s ease all;

        @media (min-width: $nav-bp) {
            display: flex;
            justify-content: flex-start;
        }

        // menu item
        >li.menu-item {
            display: block;
            position: relative;

            @media (min-width: $nav-bp) {
                display: inline-block;
                margin: 0 7px 0 0;

                &:last-child {
                    margin: 0;
                }
            }

            a {
                @extend %mobile-nav-link;

                @media (min-width: $nav-bp) {
                    padding: 8px;
                    height: 100%;
                    border: none;
                    color: $color-white;
                    width: auto;
                }

                @media (min-width:$xl) {
                    padding: 8px 16px;
                }
            }

            ul {

                li.menu-item {
                    margin-bottom: 14px;

                    a {
                        font-size: convertRem(16);
                        font-weight: $body-light;
                        text-decoration: underline;                    
                    }

                    @media (min-width:$nav-bp) {
                        margin-bottom: 0;

                        a {
                            font-size: convertRem(20);
                            text-decoration: none;
                            max-width: 100%;
                            width: 100%;
                        }
                    }

                    ul {
                        margin-top: 12px;
                        
                        @media (min-width:$nav-bp) {
                            margin-top: 0;
                        }
                    }
                }
            }
        }

        // has children
        >li.menu-item-has-children {
            position: relative;
            overflow: hidden;

            @media (min-width: $nav-bp) {
                overflow: visible;

                &.__active>ul {
                    height: auto;
                    overflow: visible;
                    display: block;
                }

                &:last-child {
                    >ul {
                        right: 0;

                        li.menu-item-has-children>button:after {
                            content: '\e919';
                        }

                        ul {
                            right: calc($nav-width - 8px);
                            left: auto;
                        }
                    }
                }

                &:nth-last-child(2) {
                    >ul {
                        right: -100%;

                        li.menu-item-has-children>button:after {
                            content: '\e919';
                        }

                        ul {
                            right: calc($nav-width - 8px);
                            left: auto;
                        }
                    }
                }
            }

            @media (min-width:$xl) {
                &:last-child {
                    >ul {
                        right: inherit;
                    }
                }
            
                &:nth-last-child(2) {
                    >ul {
                        @media (min-width:$xl) {
                            right: inherit;
            
                            li.menu-item-has-children>button:after {
                                content: '\e91a';
                            }
            
                            ul {
                                left: calc($nav-width - 22px);
                                right: auto;
                            }
                        }            
                    }
                }
            }

            >ul {
                display: none;
                z-index: 50;

                &.__active {
                    overflow-y: scroll;
                    right: 0 !important;
                    display: block;
                }
                
                &.left {
                    @media (min-width: $nav-bp) {
                        right: 0px;
                    }
                }

                @media (min-width: $nav-bp) {
                    background: $color-blackberry-800;
                    border-radius: 16px;
                    padding: 0 18px 6px 18px;
                    position: absolute;
                    width: $nav-width;
                    top: inherit;
                    bottom: inherit;
                    right: inherit;
                    height: inherit;
                    z-index: 50;
                    transform: translateX(6px);
                    display: none;
                    height: 0;
                    overflow: hidden;
                }


                li {
                    @media (min-width: $nav-bp) {
                        display: block;
                        margin: 0;
                
                
                        &:not(:last-child) {
                            &::after {
                                content: '';
                                background: $color-blackberry-300;
                                position: absolute;
                                bottom: 0;
                                left: 0;
                                height: 1px;
                                width: 100%;
                            }
                        }
                
                        a {
                            padding: 12px 0;
                        }
                    }

                    &.menu-item-has-children {
                        >button {
                            @extend %nav-link-expand-button;

                            @media (min-width:$nav-bp) {
                                &::before {
                                    content: '\e91a';
                                    font-size: convertRem(14);
                                    transform: translateX(0.5px);
                                }
                            }
                        }
                    }

                    >a {
                        color: $color-white;
                        margin: 0;
                        font-weight: $body-light;

                        &:hover,
                        &:focus {
                            @extend %text-gradient-raspberry;
                        }

                        @media (min-width: $nav-bp) {
                            line-height: inherit;

                            &::before {
                                display: none;
                            }
                        }
                    }

                    ul {
                        &.__active {
                            overflow-y: scroll;
                            right: 0 !important;
                            display: block;
                        }

                        @media (min-width: $nav-bp) {
                            top: 0;
                            left: calc($nav-width - 22px);
                            z-index: 60;
                        }
                    }
                }
            }
        }

        //comment out to show first-child icons
        @media (min-width: $nav-bp) {
            &:first-child>li.menu-item-has-children {

                &>a {
                    &::after {
                        display: none;
                    }
                }
            }
        }
    }

    /* IOS Looping nav issue */
    @supports (-webkit-touch-callout: none) {
        -webkit-transform: translateZ(0); // Fixes iOS looping nav issue
        @media (min-width:$nav-bp) { 
            -webkit-transform: none; 
        } 
    }
}