.search-results {
    &-summary {
        padding: 40px 0;

        @media (min-width:$md) {
            padding: 60px 0;
        }
    }

    .searchbar {
        background: none;
        position: relative;
        margin-top: 20px;

        @media (min-width:$md) {
            margin-top: 0;
        }

        .field_wrap {
            width: 100%;
        }

        .input_wrap {
            input {
                border-radius: 8px;
            }
        }

        .submit_wrap {
            width: auto;

            .button {
                position: absolute;
                background: none;
                border-radius: 0 8px 8px 0;
                top: 0;
                right: 0;
                width: 60px;
                display: flex;
                justify-content: center;
                align-items: center;

                .icon-search::before {
                    transition: 0.3s ease all;
                }

                &:hover,
                &:focus,
                &:active {
                    .icon-search::before {
                        color: $color-raspberry-900;
                    }
                }
            }
        }
    }

    .filter {
        padding: 0 0 40px 0;

        @media (min-width:$md) {
            padding: 0 0 60px 0;
        }

        .submit_wrap {
            margin-top: 20px;

            @media (min-width:$md) {
                justify-content: flex-end;
                margin-top: 0;
            }
        }
    }

    .listing-item {
        border: 2px solid $color-raspberry-900;
        box-shadow: 0 0 8px rgba(0, 0, 0, 0.1);
        padding: 20px;
        margin-bottom: 20px;

        @media (min-width:$md) {
            flex-direction: row;
        }

        &-image {
            border-radius: 8px 0 0 8px;
            flex: 0 0 30%;

            @media (min-width:$md) {
                margin-bottom: 0;
            }
        }

        &-content {
            padding: 10px 0;

            @media (min-width:$md) {
                padding: 1.5rem;
            }
        }
    }
}