.socialwrap {
    display: flex;
}

.socialicon {
    font-size: convertRem(18);
    display: flex;
    align-items: center;
    margin: 0 12px 24px 0;
    transition: all 0.3s;

    @media (min-width:$sm) {
        margin-bottom: 0;
    }

    &:last-child,
    &:last-child::before {
        margin-right: 0;
    }

    &::before {
        @extend %icon;
        color: $color-white;
        font-size: convertRem(24);
        margin: 0 5px 0 0;
        transition: all 0.3s;
    }

    &.__twitter {
        &::before {
            font-size: convertRem(16);
            content: '\e906';
        }
    }

    &.__facebook {
        &::before {
            content: '\e928';
        }
    }

    &.__linkedin {
        &::before {
            content: '\e93d';
        }
    }

    &.__instagram {
        &::before {
            content: '\e939';
        }
    }

    &.__pinterest {
        &::before {
            content: '\e948';
        }
    }

    &.__youtube {
        &::before {
            content: '\e969';
            // font-size: convertRem(22);
        }
    }

    &:hover,
    &:focus {
        &::before {
            @extend %text-gradient-raspberry;
        }
    }
}