.statistics {
    &-block {
        &-title {
            margin-bottom: 48px;
        }
    }
    
    &-items {
        display: grid;
        gap: 24px 16px;

        @media (min-width:$sm) {
            grid-template-columns: repeat(2, 1fr);
        }

        @media (min-width:$xl) {
            grid-template-columns: repeat(3, 1fr);
        }
    }

    &-item {
        background: $color-white;
        border-radius: 40px;
        display: flex;
        padding: 30px 61px;
        flex-direction: column;
        align-items: center;
        gap: 20px;
        position: relative;

        @media (min-width:$sm) {
            padding: 32px 61px;
        }

        &.__modal-active {
            .statistics-item-button {
                .icon::before {
                    color: $color-raspberry-500;
                }
            }
        }

        &-button {    
            transform: translate(30px, -10px);

            @media (min-width:$sm) {
                transform: none;
            }
            
            .icon::before {
                color: $color-blackberry-600;
                transition: 0.3s ease all;
            }
        }

        &-modal {
            display: none;
            position: absolute;
            width: 300px;
            z-index: 3;           
            top: 65px;

            &.__top {
                top: 18px;

                .statistics-item-modal-content {
                    &::before {
                        border-left: 12px solid transparent;
                        border-right: 12px solid transparent;
                        border-top: 12px solid $color-blackberry-500;
                        border-bottom: none;
                        top: unset;
                        bottom: -6px;
                    }
                }
            }

            &.__right {
                right: 100px;
                top: 33px;

                @media (min-width:400px) {
                    right: 150px;
                }

                @media (min-width:$sm) {
                    right: 100px;
                    top: 48px;
                }

                @media (min-width:$md) {
                    right: 230px;
                }

                .statistics-item-modal-content {                  
                    &::before {
                        border-top: 12px solid transparent;
                        border-bottom: 12px solid transparent;
                        border-left: 12px solid $color-blackberry-500;
                        border-right: none;
                        top: calc(50% - 12px);
                        right: -8px;
                        left: auto;
                    }
                }
            }

            &.__left {
                transform: translateX(330px);
                top: 45px;
                
                .statistics-item-modal-content {
                    transform: translateY(-50%);

                    &::before {
                        border-top: 12px solid transparent;
                        border-bottom: 12px solid transparent;
                        border-right: 12px solid $color-blackberry-500;
                        border-left: none;
                        top: calc(50% - 12px);
                        left: -8px;
                    }
                }
            }

            &-content {
                background-color: $color-blackberry-600;
                border-radius: 4px;
                display: flex;
                width: 200px;
                padding: 18px;
                flex-direction: column;
                align-items: flex-start;
                gap: 4px;
                position: relative;
                transform: translateX(126px);

                @media (min-width:400px) {
                    width: 250px;
                }

                @media (min-width:$sm) {
                    width: 300px;
                }

                &::before {
                    content: '';
                    width: 0;
                    height: 0;
                    border-left: 12px solid transparent;
                    border-right: 12px solid transparent;               
                    border-bottom: 12px solid $color-blackberry-500;
                    position: absolute;
                    top: -6px;
                    left: 50%;
                }

                p {
                    color: $color-white;
                    font-size: convertRem(16);
                }
            }
            
        }

        &-value {
            text-align: center;
            display: flex;
            flex-direction: column;
            align-items: center;

            &-wrap {
                display: flex;
                align-items: center;
                height: 54px;   
                transform: rotate3d(0, 0, 1, -1.9deg);
            }

            .span-text {
                @include gradient-span($gradient-raspberry-dark, $gradient-raspberry-light);
                color: $color-blackberry-500;
                display: flex;
                align-items: center;
                
                position: relative;
                width: fit-content;
                height: 100%;
                padding: 0 4px;
                font-size: convertRem(42);
                white-space: nowrap;
                z-index: 2;
        
                @media (min-width:$sm) {
                    font-size: inherit;
                }
            }
        
            img {
                flex-shrink: 0;
                height: 100%;
                position: relative;
                z-index: 1;
            }
        
            img.span-start {
                margin-right: -2px;
        
                @media (min-width:$lg) {
                }
            }
        
            img.span-end {
                margin-left: -2px;
        
                @media (min-width:$lg) {
                }
            }
        }

        &-text {
            text-align: center;
        }
    }
}