.video,
.video-local, .video-mp4 {
    @extend %bgcover;
    @include aspect(1.77, 1);
    width: 100%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;

    &-image {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 0;

        img {
            border-radius: 40px;
        }

        &::before {
            @extend %overlay;
            background: linear-gradient(0deg, rgba(25, 6, 51, 0.40) 0%, rgba(25, 6, 51, 0.40) 100%);
            border-radius: 40px;
        }
    }
}

.video-local {
    &-popup {
        line-height: 0;
        width: 90vw;
        max-width: calc(100% - 30px);
        position: relative;
        margin: 0 auto;

        &-scaler {
            width: 100%;
            height: 0;
            overflow: hidden;
            padding-top: 56.25%;

            video {
                position: absolute;
                display: block;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                box-shadow: 0 0 8px rgba(0, 0, 0, 0.6);
                object-fit: cover;
            }
        }

        .mfp-close {
            color: $color-white;
            top: -40px;
            right: -6px;
            text-align: right;
            padding-right: 6px;
            width: 100%;
        }
    }
}

.videoplay {
    width: fit-content;
    border-radius: 16px;
    background: $color-white;
    transition: all 0.3s;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 12px 24px;
    transition: 0.3s ease all;

    &-icon {
        color: $color-blackberry-500;
        position: relative;
        transition: all 0.3s;

        &::after {
            @extend %icon;
            content: '\e92f';
            color: $color-blackberry-500;
            font-size: 22px;
            margin-right: 16px;
        }
    }

    &-text {
        font-size: convertRem(20);
        font-weight: $body-semibold;
    }

    &:hover,
    &:focus {
        @include gradient-background($gradient-raspberry-dark, $gradient-raspberry-light);
    }
}

.videoblock {
    &-title {
        margin-bottom: 40px;

        @media (min-width:$md) {
            margin-bottom: 48px;
        }
    }

    &-caption {
        margin: 40px 0 48px 0;
        text-align: center;

        @media (min-width:$md) {
            margin: 30px 0 56px 0;
            text-align: left;
        }

        h2 {
            margin-bottom: 8px;
        }

        p {
            font-size: convertRem(18);
        }
    }
}