html {
    font-size: $font-size;
    font-variant-numeric: lining-nums proportional-nums;
}

body {
    background-color: $color-blackberry-50;
    color: $body-color;
    font-family: $body-family;
    overflow-x: hidden;

    &.__mobile,
    &.__modal {
        overflow: hidden;
    }
    &.__blur {
        .navwindow {
            display: block!important;
        }
    }
}

.wrapper {
    position: relative;
    transition: all 0.3s;

    &.__searchactive {
        transform: translateY(60px);
    }
}
