.filter {
    background-color: $color-blackberry-300;
    border-radius: 16px;
    padding: 16px 24px;
    display: flex;
    align-items: center;
    transform: translateY(-40px);
    position: relative;
    z-index: 3;

    @media (min-width:$lg) {
        padding: 8px 24px;
    }

    &-title {
        color: $color-white;
        margin-bottom: 0;
    }

    &-controls {
        flex-wrap: wrap;
        gap: 18px 40px;

        @media (min-width:$md) {
            flex-wrap: nowrap;
        }
    }

    .field_wrap {
        margin-bottom: 0;

        .label_wrap {
            padding: 0;
        }
        .select, .select2-css.select2-selection--multiple,
        .select2-css.select2-selection--single {
            background-color: #faf7fd;
            border-radius: 16px;
            font-size: convertRem(20);
            font-weight: $body-semibold;
            padding: 11px 24px;

            .select2-selection__arrow {
                right: 24px;
                top: 40%;

                &::before {
                    @extend %icon;
                    content: '\e918';
                    font-size: convertRem(32);
                }
                
                b {
                    border: none;
                }
            }
        }
        
        .select2-container--focus {
            .select, .select2-css.select2-selection--multiple,
            .select2-css.select2-selection--single {
                border: none;
            }
        }

        .select2-container--open {
            .select, .select2-css.select2-selection--multiple,
            .select2-css.select2-selection--single {
                border: none;

                .select2-selection__arrow {
                    &::before {
                        content: '\e91b';
                    }
                }
            }
        }
    }

    .submit_wrap {
        height: 100%;
        display: flex;
        align-items: center;

        .button {
            .icon::before {
                color: $title-color;
                margin-right: 10px;
                transition: 0.3s ease all;
            }

            &:hover,
            &:focus,
            &:active {
                .icon::before {
                    color: $color-blackberry-500;
                }
            }
        }
    }
}

.select2-container {
    .select2-dropdown.filter-dropdown {
        background: none;
        border: none;
        
        margin-top: 12px;

        .select2-results > .select2-results__options {
            background-color: $color-blackberry-50;
            border: 1px solid $color-blackberry-500;
            border-radius: 16px;
            box-shadow: 0 0 8px rgba(0, 0, 0, 0.1);
            max-height: 300px;
            padding: 12px 18px;

            &::-webkit-scrollbar-track {
                background-color: #d1d6de;
                box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.1);
                border-radius: 0 16px 16px 0;
                
            }

            &::-webkit-scrollbar {
                height: 8px;
                width: 14px;
                background-color: $color-blackberry-50;
                border-radius: 0 16px 16px 0;
            }

            &::-webkit-scrollbar-thumb {
                background-color: $color-blackberry-300;
                border-radius: 16px;
            }

            .select2-results__option {
                padding: 12px 0;
                transition: 0.3s ease all;

                &:not(:last-child) {
                    border-bottom: 1px solid $color-blackberry-300;
                    font-size: convertRem(16);
                }

                &:first-child {
                    padding-top: 0;
                }

                &:last-child {
                    padding-bottom: 0;
                }
                
                &--highlighted[aria-selected] {
                    @extend %text-gradient-raspberry;
                }

                &--selected {
                    background: none;
                    color: $title-color;
                    font-weight: $body-bold;
                }
            }
        }
    }
}