.full-width-cta {
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-radius: 40px;
    overflow: hidden;
    position: relative;
    min-height: 552px;

    &::before {
        @extend %overlay;
        background: linear-gradient(0deg, rgba(25, 6, 51, 0.60) 0%, rgba(25, 6, 51, 0.60) 100%);
        z-index: 1;
    }
    
    &-image {
        position: absolute;
        width: 100%;
        height: 100%;
        
        img {
            width: 100%;
            max-width: 100%;
            position: absolute;
            height: 100%;
            top: 0px;
        }
    }

    &-background {
        position: absolute;
        right: -67%;
        bottom: 0;
        z-index: 1;

        @media (min-width:$sm) {
            right: -30%;
        }

        @media (min-width:$lg) {
            right: 0;
        }

        svg {
            display: flex;
        }
    }

    &-text {
        padding: 40px 23px;
        position: relative;
        z-index: 2;

        @media (min-width:$lg) {
            padding: 70px 60px 70px 86px;
        }
    }

    &-title {
        color: $color-white;
        font-family: $title-font;
        font-weight: $title-extra-bold;
        line-height: 0.7;
        text-transform: uppercase;
        font-variant-numeric: lining-nums proportional-nums;
        letter-spacing: -1.2px;
        margin-bottom: 24px;
        font-size: convertRem(56);
        
        @media (min-width:$lg) {
            font-size: convertRem(88);
            line-height: 0.69;
        }
    }
}
