.listing {
    --listitem-margin-bottom: 1rem;

    @media (min-width: $md) {
        --listitem-margin-bottom: 2rem;
    }

    &-items {
        padding: 56px 0;

        &.__case-studies {
            padding-bottom: 0;
        }

        .case-studies-item {
            &:hover,
            &:focus {
                @extend %image-zoom;
                
                .button.__dark {
                    @extend %dark-button-hover;
                }
            }
        }

        .post-item {
            margin-bottom: 30px;

            @media (min-width:$sm) {
                margin-bottom: 40px;
                height: calc(100% - 40px);
            }
        }

        .post-item,
        .case-studies-item {
            &:hover,
            &:focus {
                transform: translateY(-10px);

                .post-image img
                .case-studies-item-image img {
                    transform: scale(1.15);
                }
            }
        }
    }    
}