/* Helper mixins */
@mixin aspect($width: 16, $height: 9) {

    @if $width {
        aspect-ratio: math.div($width, $height);

        @supports not (aspect-ratio: math.div($width, $height)) {
            &::before {
                content: '';
                float: left;
                padding-top: calc((#{$height} / #{$width}) * 100%);
            }

            &::after {
                content: '';
                display: block;
                clear: both;
            }
        }
    }

    @else {
        aspect-ratio: auto;

        @supports not (aspect-ratio: auto) {
            &::before {
                content: none;
            }

            &::after {
                content: none;
            }
        }
    }
}

// Mixin for gradient buttons
@mixin gradient-background($color1, $color2) {
    background: linear-gradient(180deg, $color1 0%, $color2 100%);
}

// Mixin for title span
@mixin gradient-span($color1, $color2) {
    background: linear-gradient(90deg, $color1 0%, $color2 100%);
}

@mixin gradient-progressbar($color1, $color2) {
    background: linear-gradient(270deg, $color1 0%, $color2 100%);
}