$slider-height-mobile: 300px;
$slider-height: 600px;
$button-width-height: 60px;

.slider {
    position: relative;

    &-items {
        max-height: $slider-height-mobile;
        overflow: hidden;
        @media (min-width: $md) {
            max-height: $slider-height;
        }

        &.slick-initialized {
            max-height: none;
            overflow: visible;
        }
    }

    &-button {
        position: absolute;
        top: 50%;
        z-index: 100;
        transform: translateY(-50%);
        left: 0;

        &.__next {
            left: auto;
            right: 0;
        }
    }

    &-tabpanel-panel {
        display: none;

        .progress-wrap {
            display: none;
        }

        &.__active {
            display: block;

            .progress-wrap {
                display: block;
            }
        }
    }

    .slick-dots {
        position: absolute;
        bottom: 20px;
        left: 50%;
        display: block;
        width: auto;
        padding: 0;
        margin: 0;
        list-style: none;
        text-align: center;
        transform: translateX(-50%);

        li {
            position: relative;
            display: inline-block;
            margin: 0 5px;
            padding: 0;
            cursor: pointer;
            vertical-align: middle;

            &.slick-active button {
                @include gradient-background($gradient-raspberry-dark, $gradient-raspberry-light);
            }

            button {
                font-size: 0;
                line-height: 0;
                display: block;
                width: 16px;
                height: 16px;
                padding: 0;
                border-radius: 50%;
                cursor: pointer;
                background: $color-blackberry-500;
                transition: all 0.3s;

                &:hover,
                &:focus {
                    opacity: 0.6;
                }
            }
        }
    }

    .progress {
        display: block;
        width: 100%;
        height: 6px;
        border-radius: 10px;
        overflow: hidden;

        background-color: #d8d8d8;
        background-image: linear-gradient(to right, $color-blackberry-500, $color-blackberry-500);
        background-repeat: no-repeat;
        background-size: 0 100%;

        transition: background-size .4s ease-in-out;

        &-wrap {
            position: relative;
            margin: 24px auto 0 auto;

            @media (min-width: $lg) {
                margin: 22px auto 0 auto;
            }
        }
    }
}

.posts-slider {
    padding: 68px 0 40px 0;
    overflow: hidden;

    @media (min-width:$xl) {
        padding: 70px 0 40px 0;
    }

    &-title {
        font-size: convertRem(56);
        width: fit-content;
        position: relative;
        margin-bottom: 14px;

        @media (min-width:$md) {
            font-size: convertRem(88);
            margin-bottom: 28px;
        }

        @media (min-width:$xxl) {
            font-size: convertRem(88);
            margin-bottom: 0;
        }

        span {
            position: relative;
            z-index: 2;

            text-shadow: -2px 1px 0 $color-white,
                0px 2px 0 $color-white,
                1px 1px 0 $color-white,
                -1px 1px 0 $color-white
        }

        svg {
            position: absolute;
            bottom: -16px;
            left: 0;
            z-index: 1;
            transform: scaleY(0.6);
        }

    }

    &-header {
        @media (min-width:$md) {
            margin-bottom: 42px;
        }
    }

    &-controls {
        margin-right: -16px;
        padding-right: 0;

        @media (min-width:$md) {
            margin-right: inherit;
            padding-right: inherit;
        }
    }

    &-filters {
        display: flex;
        padding: 10px 32px;
        flex-direction: row;
        align-items: flex-start;
        gap: 10px;
        border-radius: 100px;
        background: #DCD9DF;

        &-wrap {
            overflow-y: auto;
            display: flex;
            align-items: center;

            @media (min-width:$md) {
                overflow: unset;
            }
        }

        .filter-button {
            color: $title-color;
            display: flex;
            font-weight: $body-semibold;
            font-size: convertRem(20);
            padding: 8px 16px;
            align-items: center;
            gap: 8px;
            white-space: nowrap;
            transition: 0.3s ease all;
            position: relative;

            &::after {
                content: '';
                @include gradient-background($gradient-raspberry-dark, $gradient-raspberry-light);
                position: absolute;
                left: 16px;
                bottom: 0;
                height: 3px;
                width: 0%;
                transition: 0.3s ease all;
            }

            &:hover,
            &:focus {
                @extend %text-gradient-raspberry;
            }

            &.__active {
                &::after {
                    width: calc(100% - 32px);
                }
            }
        }
    }

    .view-all {
        margin: 64px 0 0 0;
        white-space: nowrap;
        width: 100%;

        @media (min-width:$lg) {
            margin: 0 0 0 63px;
            width: fit-content;
        }
    }

    .slider-items {
        margin: 0 -5px; //matches value of .post-item padding

        @media (min-width:$md) {
            margin: 0 -8.5px; //matches value of .post-item padding
        }
    }

    .slider-controls {
        margin: 36px auto 0 auto;
        
        @media (min-width:$lg) {
            margin: 0;
            position: absolute;
            right: -16%;
            bottom: -25px;
        }

        @media (min-width:$xl) {
            right: -8%;
        }
    }

    .post-item {
        padding: 0 5px;
        height: 100%;

        @media (min-width:$md) {
            padding: 0 8.5px;
        }
    }

    .progress-wrap {
        bottom: -18px;

        @media (min-width:$md) {
            bottom: -3px;
        }

        @media (min-width:$lg) {
            padding-right: 120px;
            margin-top: 74px;
        }

        @media (min-width:$xl) {
            padding-right: 169px;
        }
    }

    .slick-list {
        padding-left: 0 !important;
        overflow: visible;
    }

    .sliderbutton {
        height: 50px;
        width: 52px;

        &.__next {
            margin-left: 44px;
        }
    }
}

.signposting-slider {
    //remove slick styling when not a mobile device
    @media (min-width:$sm) {
        .slider-items {
            max-height: unset;
        }
    }
}

.gallery-slider {
    &-image {
        border-radius: 40px;
        overflow: hidden;

        img {
            @include aspect(3, 2);

            @media (min-width:$lg) {
                @include aspect(1.83, 1);
            }
        }
    }

    &-title {
        margin-bottom: 48px;

        @media (min-width:$lg) {
            margin-bottom: 52px;
        }
    }

    &-caption {
        margin: 32px 30px 150px 30px;
        text-align: center;

        @media (min-width:$lg) {
            flex: 0 0 auto;
            width: 33.33333333%;
            min-height: 108px; //used to correctly position dots
            margin: 130px auto 0 auto;
        }

        &-title {
            margin-bottom: 8px;
        }

        &-text {
            font-size: convertRem(18);
        }
    }

    .slider-items {
        max-height: 100%;
    }

    .slick-dots {
        bottom: 104px;
        width: 100%;
        display: flex;
        justify-content: center;
        margin: 0 auto;

        @media (min-width:$lg) {
            bottom: 170px;
        }
    }

    .slider-button {
        top: auto;
        bottom: 30px;
        transform: none;
        left: 15px;

        &.__next {
            left: auto;
            right: 15px;
        }

        @media (min-width:$lg) {
            bottom: auto;
            left: -55px;
            transform: translateY(-50%);
            top: 50%;

            &.__next {
                left: auto;
                right: -55px;               
            }
        }

        .sliderbutton {
            width: 50px;
            height: 50px;
        }
    }
}

.slideritem {
    @extend %bgcover;
    height: $slider-height-mobile;
    position: relative;

    @media (min-width: $md) {
        height: $slider-height;
    }

    &-wrap {
        width: 100%;
        height: $slider-height-mobile;
        display: flex;
        align-items: center;
        padding: 0 $button-width-height;

        @media (min-width: $md) {
            height: $slider-height;
        }
    }

    &-title {
        color: $title-color;
        font-size: convertRem(26);
        margin: 0 0 20px 0;
    }

    &-article {
        font-size: convertRem(18);
        color: $body-color;
        margin: 0 0 20px 0;
    }

    &-image {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: $slider-height;
    }
    &-link {
        @extend .button-wrap;
    }
}

// Slider buttons
// Arrows
.sliderbutton {
    @extend .button;
    display: flex;
    justify-content: center;
    align-items: center;
    width: $button-width-height;
    height: $button-width-height;
    padding: 0;

    &::after {
        @extend %icon;
        content: '\e91a';
        color: #fff;
        font-size: 28px;
    }

    &.__prev {
        &::after {
            content: '\e919';
        }
    }
}

// Autoplay toggle
.slick-autoplay-toggle-button {
    @extend .button;
    position: absolute;
    bottom: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 60px;
    height: 60px;
    padding: 0;
    z-index: 1;

    .slick {
        &-sr-only {
            @extend .visually-hidden;
        }

        &-play-icon,
        &-pause-icon {
            &::before {
                @extend %icon;
                color: #fff;
            }
        }

        &-pause-icon::before {
            content: '\e944';
        }

        &-play-icon::before {
            content: '\e949';
        }
    }
}

.slick-sr-only {
    @extend .visually-hidden;
}