.faq {
    .tabpanel {
        padding: 0;
        position: relative;
        z-index: 5;

        &-head {
            background-color: $color-blackberry-300;
            padding: 8px 24px;
        }

        &-tab {
            padding-top: 8px;
            padding-bottom: 8px;

            @media (min-width:$lg) {
                width: 16.6666667%;
            }

            &:hover,
            &:focus {
                background-color: $color-blackberry-400;
                padding-left: 16px;

                &::before {
                    content: none;
                }
            }

            &.__active {
                &:hover,
                &:focus {
                    background-color: $color-blackberry-100;
                }
            }
        }

        &-panel {
            padding: 32px 0 40px 0;

            @media (min-width:$md) {
                padding: 67px 0 107px 0;
            }
        }
    }
}