.header {
    background: $color-clear;
    padding: 14px 0;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 55555;

    @media (min-width: $lg) {
        padding: 28px 0;
    }

    &-logo {
        display: block;
        width: 96px;
        
        @media (min-width:$lg) {
            width: 126px;
        }
    }

    &-opts {
        display: flex;
        justify-content: flex-end;
        align-items: center;
    }

    &-cta {
        white-space: nowrap;
        margin-bottom: 41px;

        @media (min-width:$nav-bp) {
            margin: 0 0 0 32px;
        }
    }
}