.breadcrumb {
    background: none;
    margin: 0;
    padding: 0;
    border-bottom: 1px solid #ccc;

    &-list {
        padding: 15px 0;
        display: flex;

        >li {
            margin-right: 15px;
            padding-right: 15px;
            position: relative;

            &::before {
                @extend %icon;
                content: '\e91a';
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                right: -10px;
                font-size: 18px;
            }

            &:last-of-type {
                margin-right: 0;
                padding-right: 0;

                &::before {
                    content: none;
                }
            }
        }

        a,
        span {
            font-size: convertRem(18);

            &[aria-current] {
                color: $color-raspberry-800;
            }
        }

        a:hover,
        a:focus {
            color: $color-raspberry-900;
        }
    }
}