// Flexible blocks structure
.flexibleblocks {
    padding-top: 30px;

    @media (min-width: $lg) {
        padding-top: 65px;
    }

    &-block {
        margin-bottom: 30px;

        @media (min-width: $lg) {
            margin-bottom: 64px;
        }

        &.__nomargin {
            margin-bottom: 0;
        }

        &.__smallmargin {
            margin-bottom: 15px;

            @media (min-width: $lg) {
                margin-bottom: 30px;
            }
        }

        &.__mediummargin {
            // same as no classes (default)
            margin-bottom: 30px;

            @media (min-width: $lg) {
                margin-bottom: 64px;
            }
        }

        &.__largemargin {
            margin-bottom: 50px;

            @media (min-width: $lg) {
                margin-bottom: 80px;
            }
        }

        &:last-child:has(.two-column-block.__frameless-image) {
            margin-bottom: 0;

            .__frameless-image {
                border-radius: 40px 40px 0 0;
            }
        }
    }
    
}

