.case-studies {
    &-item {
        background-color: $color-white;
        border-radius: 40px;
        overflow: hidden;
        margin-bottom: 32px;

        &:last-child {
            margin-bottom: 6px;
        }

        &-content {
            padding: 32px;

            @media (min-width:$sm) {
                padding: 32px 18px 32px 0;
            }

            @media (min-width:$md) {
                padding: 32px;
            }
        }

        &-image {
            height: 100%;
            overflow: hidden;
            
            img {
                transition: 0.3s ease all;
            }
        }

        &-date {
            margin-bottom: 31px;
        }

        &-title {
            @media (min-width:$xl) {
                margin-bottom: 8px;
                padding-right: 160px;
            }
        }

        &-text {
            @media (min-width:$xl) {
                padding-right: 296px;
            }
        }

        &-cta {
            margin-top: 33px;
        }
    }
}