/* accordion */
.accordion {
    border-radius: 8px;
    overflow: hidden;
    transition: 0.3s ease all;

    &:hover {
        outline: inset;
        outline-color: $color-raspberry-600;
        outline-width: 2px;
        outline-style: solid;

        .accordion-head,
        .accordion-content {
            border-color: $color-clear;
        }
    }

    &:not(:last-child) {
        margin-bottom: 16px;
    }

    &-head {
        border-radius: 8px;
        border: 1px solid $title-color;
        color: $title-color;
        font-size: convertRem(20);
        font-weight: $body-semibold;
        padding: 16px 24px;
        position: relative;
        text-align: left;
        transition: all 0.3s;
        cursor: pointer;
        
        .icon {
            background-color: $color-blackberry-500;
            width: 32px;
            height: 32px;
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
            transition: 0.3s ease all;

            &::after {
                @extend %text-gradient-raspberry;
                content: '\e94a';
                font-size: convertRem(24);
                left: inherit;
                position: absolute;
                right: 28px;
                transition: all 0.3s;
            }
        }

        &.__active {
            background-color: $color-white;
            border-color: $color-white;
            border-radius: 8px 8px 0 0;
            color: $title-color;

            .icon::after {
                transform: rotate(45deg);
            }
        }

        &:hover {
            .icon {
                @include gradient-background($gradient-raspberry-dark, $gradient-raspberry-light);

                &::after {
                    background: linear-gradient(180deg, $color-blackberry-500 0%, $color-blackberry-500 100%);
                    -webkit-background-clip: text;
                    -webkit-text-fill-color: transparent;
                }
            }
        }
    }

    &-content {
        background-color: $color-white;
        border: 1px solid $color-white;
        border-radius: 0 0 8px 8px;
        color: $title-color;
        padding: 12px 24px 24px 24px;
        display: none;

        .usercontent {
            margin-bottom: 0 !important;

            p {
                color: $title-color;
            }
        }
    }
}

.__background-dark {
    &:hover,
    &:focus {
        outline-width: 2px;
    }
    
    .accordion {
        &-head {
            border: 1px solid $color-white;
            color: $color-white;

            &.__active {
                color: $title-color;
            }

            .icon {
                background-color: $color-blackberry-50;
            }
        }
    }
}