.banner {
    position: relative;
    min-height: 350px;
    align-items: center;
    display: flex;

    &.__flex {
        margin-bottom: 142px;

        &::before {
            @extend %overlay;
            background-color: $color-blackberry-500;
            opacity: 1;         
            border-radius: 0 0 40px 40px;
        }

        @media (min-width: $md) {
            margin-bottom: 70px;
            min-height: 350px;
        }

        @media (min-width: $lg) {
            margin-bottom: 140px;
            min-height: 400px;
        }

        @media (min-width: $xl) {
            min-height: 480px;
        }
        
        @media (min-width:$xxl) {
            min-height: 512px;
        }

        @media (min-width: 1600px) {
            min-height: 512px;
        }

        .banner {
            &-background {
                position: absolute;
                left: -229px;
                height: 100%;
                width: 200%;
                top: 258px;
                overflow: hidden;
                z-index: 1;

                @media (min-width:$md) {
                    left: unset;
                    right: -50%;
                    height: 120%;
                    width: 120%;
                    bottom: -20px;
                    top: unset;
                }

                @media (min-width:$xl) {
                    right: -45%;
                }

                @media (min-width:$xxl) {
                    right: -55%;
                }

                @media (min-width:1800px) {
                    right: -58%;
                }

                &-wrap {
                    overflow: hidden;
                    position: absolute;
                    height: 100%;
                    top: 0;
                    right: 0;
                    width: 100%;
                }

                svg {
                    position: absolute;
                    width: 1300px;
                    height: 952px;
                    transform: rotate(15deg);
                    opacity: 0.4;

                    @media (min-width:$md) {
                        transform: rotate(3.922deg);
                    }
                }
            }

            &-content {
                @media (max-width:$md) {
                    padding-top: 158px; 
                    margin-bottom: -80px;
                    padding-bottom: 0;        
                }
            }
        }
    }

    &.__home {
        margin-bottom: 142px;

        &::before {
            @extend %overlay;
            background-color: $color-blackberry-500;
            opacity: 1;
            border-radius: 0 0 40px 40px;
        }

        @media (min-width: $md) {
            margin-bottom: 70px;
            min-height: 350px;
        }

        @media (min-width: $lg) {
            min-height: 400px;
        }

        @media (min-width: $xl) {
            min-height: 480px;
        }

        @media (min-width:$xxl) {
            margin-bottom: 90px;
            min-height: 500px;
        }

        .banner {
            &-background {
                position: absolute;
                left: -229px;
                height: 100%;
                width: 200%;
                top: 115px;
                z-index: 1;

                @media (min-width:$lg) {
                    left: unset;
                    right: -50%;
                    height: 141%;
                    width: 100%;
                    bottom: -20px;
                    top: unset;
                }

                @media (min-width:$xl) {
                    right: -25%;
                }

                @media (min-width:$xxl) {
                    right: -18%;
                }

                &-wrap {
                    overflow: hidden;
                    position: absolute;
                    height: 100%;
                    top: 0;
                    right: 0;
                    width: 100%;
                }

                svg {
                    position: absolute;
                    width: 100%;
                    height: 1701.46px;
                    transform: rotate(15deg);
                    opacity: 0.4;

                    @media (min-width:$lg) {
                        transform: rotate(3.922deg);
                        animation: infinite linear backgroundRotate 20s;
                        animation-iteration-count: infinite;
                    }


                    @keyframes backgroundRotate {
                        0% {
                            transform: rotate(0deg);
                        }

                        100% {
                            transform: rotate(-360deg);
                        }
                    }


                    /* Use rotate instead, but only if it is supported */
                    @supports (rotate: 0deg) {
                        @keyframes backgroundRotate {
                            0% {
                                rotate: 0deg;
                            }

                            100% {
                                rotate: -360deg;
                            }
                        }
                    }
                }
            }

            &-title {
                .span-text{
                    @include gradient-span($gradient-raspberry-dark, $gradient-raspberry-light);
                    color: $color-blackberry-500;
                    display: flex;
                    align-items: center;
                    position: relative;
                    height: 100%;
                    width: fit-content;
                    font-size: convertRem(42);
                    white-space: nowrap;
                    z-index: 2;

                    @media (min-width:$sm) {
                        font-size: inherit;
                    }

                }

                .banner-span-wrap {
                    display: flex;
                    align-items: center;
                    height: 47px;
                    transform: rotate3d(0, 0, 1, -1.9deg);
                    
                    @media (min-width:$lg) {
                        height: 86px;
                    }
                }
                    
                img {
                    flex-shrink: 0;
                    height: 100%;
                    position: relative;
                    z-index: 1;
                }

                img.span-start {
                    margin-right: -4px;
                }

                img.span-end {
                    margin-left: -3px;
                }
            }

            &-content {
                padding-top: 130px;

                @media (max-width:$lg) {
                    margin-bottom: -40px;
                    padding-top: 158px;
                    padding-bottom: 0;
                }
            }

            &-image {
                @media (min-width:$md) {
                    max-width: 592px;
                }
            }
        }

        .button-wrap {
            margin-top: 49px;

            @media (min-width:$md) {
                margin-top: 26px;
            }
        }
    }

    &.__flex.__listing,
    &.__flex.__listing.__no-image,
    &.__flex.__faq {
        margin-bottom: 0;

        .banner {
            &-background {
                @media (min-width:$lg) {
                    right: -60%;
                }
                
                @media (min-width:$xl) {
                    right: -75%;
                }
            }
        }
    }

    &.__flex.__faq {
        margin-bottom: -29px;
    }

    &.__flex.__post {
        @media (min-width:$lg) {
            margin-bottom: 50px;
            min-height: 736px;
        }
        
        .banner {
            &-image {
                @media (min-width:$md) {
                    margin: 0 0 0 auto;
                    max-width: 570px;
                }
            }
        }
    }

    &.__flex.__service {
        margin-bottom: 30px;
        overflow: hidden;

        @media (min-width:$lg) {
            margin-bottom: 60px;
        }

        .banner {
            &-content {
                margin: 0 0 30px 0;
                
                @media (min-width:$md) {
                    margin: 0;
                }
            }

            &-image {
                display: flex;
                align-items: center;
                bottom: auto;
                margin-left: 0;

                @media (min-width:$lg) {
                    display: block;
                    bottom: auto;
                    margin-left: auto;
                    top: 222px; //match to margin
                    margin-bottom: -222px; //match to top
                }
            }
        }
    }

    &.__animation-paused {
        .banner-background {
            svg {
                animation-play-state: paused;
            }
        }
    }

    &.__align-centre {
        @media (min-width:$md) {
            text-align: center;

            .banner-wrap-row {
                justify-content: center;
            }
        }
    }

    &.__align-right {
        @media (min-width:$md) {
            text-align: right;

            .banner-wrap-row {
                justify-content: flex-end;
            }
        }
    }

    &.__dark-text {

        .banner-title,
        .banner-text {
            color: $color-black;
        }

        .banner-image {
            &::before {
                background-color: $color-white;
            }
        }
    }

    &.__small {
        @media (min-width: $md) {
            min-height: 300px;
        }

        @media (min-width: $lg) {
            min-height: 350px;
        }

        @media (min-width: $xl) {
            min-height: 400px;
        }
    }

    &.__tall {
        @media (min-width: $md) {
            min-height: 500px;
        }

        @media (min-width: $lg) {
            min-height: 550px;
        }

        @media (min-width: $xl) {
            min-height: 600px;
        }
    }

    &.__no-image {
        margin-bottom: 0;

        @media (min-width: $lg) {
            margin-bottom: 35px;
        }

        .banner-background {
            @media (min-width:$md) {
                top: 140px;
            }

            svg {
                @media (min-width:$md) {
                    transform: rotate(45deg);
                }
            }
        }

        .banner-content {
            @media (max-width:$md) {
                margin: 0;
                padding-bottom: 116px;
            }
        }
    }

    &-wrap {
        width: 100%;

        &::before {
            content: "";
            position: absolute;        
            background-color: transparent;
            bottom: 0;
            left: 0;
            height: 40px;
            width: 40px;
            border-bottom-left-radius: 40px;
            box-shadow: -9px 13px 0px 7px $color-blackberry-50;
            z-index: 3;
        }

        &::after {
            content: "";
            position: absolute;        
            background-color: transparent;
            bottom: 0;
            right: 0;
            height: 40px;
            width: 40px;
            border-bottom-right-radius: 40px;
            box-shadow: 22px 21px 0px 20px $color-blackberry-50;
            z-index: 2;
        }
    }

    &-image {
        display: block;
        position: relative;
        bottom: -100px;
        right: 0;
        width: auto;
        height: auto;
        margin: 0 auto;
        overflow: hidden;
        z-index: 3;

        @media (min-width:$md) {
            margin: 0 0 0 auto;
            max-width: 592px;
            bottom: 0;
            display: flex;
            align-items: center;
        }

        @media (min-width:$lg) {
            bottom: -70px;
            top: 140px;
            max-width: 530px;
        }

        @media (min-width:$xxl) {
            top: 110px;
        }
        
        @media (min-width:1600px) {
            max-width: 592px;
        }

        img.banner-clip-path {
            @include aspect(1.02, 1);
            @extend %clip-path-home-rotate;
        }
    }

    &-content {
        position: relative;
        z-index: 5;
        color: $color-white;
        font-size: convertRem(18);
        padding: 158px 0 116px 0;
    
        @media (min-width:$nav-bp) {
            padding: 222px 0 116px 0;
        }
    }

    &-title {
        font-family: $title-font;
        font-size: convertRem(56);
        line-height: 0.75;
        margin-bottom: 16px;
        text-transform: uppercase;

        @media (min-width: $sm) {
            font-size: convertRem(64);
        }

        @media (min-width: $md) {
            font-size: convertRem(76);
        }

        @media (min-width:$lg) {
            font-size: convertRem(80);
        }

        @media (min-width: $xl) {
            font-size: convertRem(88);
            margin-bottom: 24px;
        }
    }

    &-text {
        font-size: convertRem(20);
    }

    &-link {
        margin: 20px 0 0 0;
    }

    &-pause {
        @include gradient-background($gradient-raspberry-dark, $gradient-raspberry-light);
        border-radius: 16px;
        width: 40px;
        height: 40px;
        position: absolute;
        bottom: 40px;
        display: none;

        @media (min-width:$lg) {
            display: block;
        }

        .icon {
            &::before {
                @extend %icon;
                font-size: convertRem(20);
                line-height: 1;
                color: $title-color;
                display: flex;
                align-items: center;
                justify-content: center;
            }
            &.__play {
                &::before {
                    content: '\e949';
                }
            }
    
            &.__pause {
                &::before {
                    content: '\e944';
                }
            }
        }

        &:hover,
        &:focus {
            background: $color-white !important;
        }
    }

    &-date {
        font-size: convertRem(20);
        margin-bottom: 40px;

        @media (min-width:$md) {
            margin-bottom: 48px;
        }
    }

    &-author {
        margin-top: 40px;

        @media (min-width:$md) {
            margin-top: 60px;
        }

        &-image {
            @extend %clip-path-home;
            width: 64px;
            margin-right: 27px;
        }

        &-content {
            span {
                font-size: convertRem(20);
            }

            span:first-child {
                padding-right: 11px;
                position: relative;

                &::after {
                    content: '';
                    position: absolute;
                    top: 0;
                    right: 0;
                    height: 100%;
                    width: 1px;
                    @include gradient-background($gradient-raspberry-dark, $gradient-raspberry-light);
                }
            }

            span:last-child {
                padding-left: 11px;
            }
        }
    }
}