.signposting {
    &-block {
        &-header {
            margin-bottom: 42px;

            &:has(.signposting-cta) {
                @media (min-width:$sm) {
                    margin-bottom: 80px;
                }
            }            

            .signposting-cta {
                margin-top: 24px;
            }
        }
    }

    &-item {
        background: $color-white;
        border-radius: 16px;
        display: flex;
        width: 320px;
        padding: 24px;
        flex-direction: column;
        align-items: flex-start;
        gap: 16px;
        transition: 0.3s ease all;
        position: relative;
        z-index: 1;
        width: 100%;

        @media (min-width:$sm) {
            margin-bottom: 16px;
        }

        &-background {
            @include gradient-background($gradient-raspberry-dark, $gradient-raspberry-light);
            border-radius: 16px;
            position: absolute;
            top: 1px;
            bottom: 1px;
            left: 1px;
            right: 1px;
            transition: 0.3s ease all;
            z-index: 0;
        }

        &-wrap {
            position: relative;
            margin: 0 10px;

            @media (min-width:$sm) {
                margin: 0;
            }

            &:hover,
            &:focus {
                .signposting-item-background {
                    transform: rotate(2deg);
                }

                .signposting-item {
                    transform: rotate(-2deg);
                }
            }
        }

        &-icon {
            width: 120px;
            height: 120px;
            display: none;

            @media (min-width:$sm) {
                display: block;
            }
        }
    }

    &-cta {
        margin-top: 38px;
    }
    .slider-items {
        padding-top: 7px;
    }
}