.resources {
    &-title {
        margin-bottom: 24px;

        @media (min-width:$md) {
            margin-bottom: 32px;
        }
    }

    &-item {
        background-color: $color-white;
        border-radius: 40px;
        padding: 24px;
        flex-wrap: wrap;

        @media (min-width:$md) {
            flex-wrap: nowrap;
        }

        &:not(:last-child) {
            margin-bottom: 24px;
        }

        &:hover,
        &:focus {
            .resources-item-icon {
                @include gradient-background($gradient-raspberry-dark, $gradient-raspberry-light);

                .icon::before {
                    color: $color-blackberry-500;
                }
            }
        }

        &.__pdf {
            .resources-item {
                &-image {
                    .icon::before {
                        content: '\e92b';
                    }
                }
            }
        }

        &-content {
            color: $color-blackberry-500;
            flex: 1 0 100%;
            padding: 0 0 16px 0;
            position: relative;
            order: 1;

            @media (min-width:$md) {
                flex: 1 1 100%;
                padding: 0 24px;
                order: unset;

                &::before {
                    content: '';
                    background-color: $color-blackberry-50;
                    position: absolute;
                    width: 1px;
                    height: calc(100% + 48px);
                    top: -24px;
                    left: 0;
                }
            }
        }

        &-title {
            margin-bottom: 8px;
        }

        &-image {
            flex: 0 0 61px;
            margin-right: 24px;
            order: 2;
            
            @media (min-width:$md) {
                margin: auto 0;
                order: unset;
            }

            .icon::before {
                content: '\e945';
                color: $color-blackberry-500;
                font-size: convertRem(52);
            }
        }

        &-icon {
            background: $color-blackberry-500;
            border-radius: 16px;
            flex-shrink: 0;
            padding: 16px;
            transition: 0.3s ease all;
            order: 3;
            
            @media (min-width:$md) {
                max-height: 60px;
                margin: auto 0;
                order: unset;
            }

            .icon {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 24px;
                height: 24px;

                &::before {
                    content: '\e907';
                    color: $color-white;
                }
            }
        }
    }
}