/* accordion */
.accordion {
  border-radius: 8px;
  overflow: hidden;
  transition: 0.3s ease all;
}
.accordion:hover {
  outline: inset;
  outline-color: #ca48db;
  outline-width: 2px;
  outline-style: solid;
}
.accordion:hover .accordion-head,
.accordion:hover .accordion-content {
  border-color: transparent;
}
.accordion:not(:last-child) {
  margin-bottom: 16px;
}
.accordion-head {
  border-radius: 8px;
  border: 1px solid #190633;
  color: #190633;
  font-size: 1rem;
  font-weight: 600;
  padding: 16px 24px;
  position: relative;
  text-align: left;
  transition: all 0.3s;
  cursor: pointer;
}
.accordion-head .icon {
  background-color: #190633;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.3s ease all;
}
.accordion-head .icon::after {
  content: "\e94a";
  font-size: 1.2rem;
  left: inherit;
  position: absolute;
  right: 28px;
  transition: all 0.3s;
}
.accordion-head.__active {
  background-color: #FFFFFF;
  border-color: #FFFFFF;
  border-radius: 8px 8px 0 0;
  color: #190633;
}
.accordion-head.__active .icon::after {
  transform: rotate(45deg);
}
.accordion-head:hover .icon {
  background: linear-gradient(180deg, #FF2CDE 0%, #FF72E9 100%);
}
.accordion-head:hover .icon::after {
  background: linear-gradient(180deg, #190633 0%, #190633 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.accordion-content {
  background-color: #FFFFFF;
  border: 1px solid #FFFFFF;
  border-radius: 0 0 8px 8px;
  color: #190633;
  padding: 12px 24px 24px 24px;
  display: none;
}
.accordion-content .usercontent {
  margin-bottom: 0 !important;
}
.accordion-content .usercontent p {
  color: #190633;
}

