.__theme-business {
    %dark-button-hover {
        @include gradient-background($gradient-blueberry-dark, $gradient-blueberry-light);
    }

    .accordion {
        &:hover {
            outline-color: $color-blueberry-600;
        }

        &-head {
            .icon {
                &::after {
                    @extend %text-gradient-blueberry;
                }
            }

            &:hover {
                .icon {
                    @include gradient-background($gradient-blueberry-dark, $gradient-blueberry-light);
                }
            }
        }
    }

    .banner {
        &.__home {
            .banner {
                &-title {
                    .span-start, .span-end {
                        display: none;
                    }
                    .span-text{
                        padding: 0px 35px;
                        border-radius: 10px;
                        @include gradient-span($gradient-blueberry-dark, $gradient-blueberry-light);
                    }
                }
            }
        }

        &.__flex {
            .banner {
                &-background {
                    svg {
                        height: 1452px;
                    }
                }
            }
        }

        &-image {
            img.banner-clip-path {
                @extend %clip-path-business-rotate;
            }
        }

        &-pause {
            @include gradient-background($gradient-blueberry-dark, $gradient-blueberry-light);
        }

        &-author {
            &-content {
                span:first-child {
                    &::after {
                        @include gradient-background($gradient-blueberry-dark, $gradient-blueberry-light);
                    }
                }
            }

            &-image {
                @extend %clip-path-business;
            }
        }
    }

    .statistics {
        &-item {
            .span {
                &-text {
                    border-radius: 10px;
                    padding: 0 25px;
                }
                &-start, &-end {
                    display: none;
                }
            }
        }
    }

    .button {
        &:hover,
        &:focus {
            @include gradient-background($gradient-blueberry-dark, $gradient-blueberry-light);
        }

        &.__dark {
            &:hover,
            &:focus {
                @include gradient-background($gradient-blueberry-dark, $gradient-blueberry-light);
            }
        }

        &.__light {
            &:hover,
            &:focus {
                @include gradient-background($gradient-blueberry-dark, $gradient-blueberry-light);
            }
        }

        &.__gradient-dark {
            @include gradient-background($gradient-blueberry-dark, $gradient-blueberry-light);
        }

        &.__gradient-light {
            @include gradient-background($gradient-blueberry-dark, $gradient-blueberry-light);
        }

        &.__icon {
            &:hover,
            &:focus,
            &:active {
                @include gradient-background($gradient-blueberry-dark, $gradient-blueberry-light);
            }
        }

        &.__inline {
            .icon {
                @include gradient-background($gradient-blueberry-dark, $gradient-blueberry-light);
            }
        }
    }

    .nav {
        ul {
            // has children
            >li.menu-item-has-children {
                >ul {
                    li {
                        >a {
                            &:hover,
                            &:focus {
                                @extend %text-gradient-blueberry;
                            }
                        }
                    }
                }
            }
        }
    }

    .posts-slider {
        &-filters {
            .filter-button {
                color: $title-color;
                display: flex;
                font-weight: $body-semibold;
                font-size: convertRem(20);
                padding: 8px 16px;
                align-items: center;
                gap: 8px;
                white-space: nowrap;
                transition: 0.3s ease all;

                &:hover,
                &:focus {
                    @extend %text-gradient-blueberry;
                }

                &::after {
                    @include gradient-background($gradient-blueberry-dark, $gradient-blueberry-light);
                }
            }
        }
    }

    .resources {
        &-item {
            &:hover,
            &:focus {
                .resources-item-icon {
                    @include gradient-background($gradient-blueberry-dark, $gradient-blueberry-light);
                }
            }
        }
    }

    .select2-container {
        .select2-dropdown.filter-dropdown {
            .select2-results > .select2-results__options {
                .select2-results__option {
                    &--highlighted[aria-selected] {
                        @extend %text-gradient-blueberry;
                    }
                }
            }
        }
    }

    .signposting {
        &-item {
            &-background {
                @include gradient-background($gradient-blueberry-dark, $gradient-blueberry-light);
            }
        }
    }

    .slider {
        .slick-dots {
            li {
                &.slick-active button {
                    @include gradient-background($gradient-blueberry-dark, $gradient-blueberry-light);
                }
            }
        }
    }

    .socialicon {
        &:hover,
        &:focus {
            &::before {
                @extend %text-gradient-blueberry;
            }
        }
    }

    .statistics {
        &-item {
            &.__modal-active .statistics-item-button .icon::before {
                color: $color-blueberry-500;
            }
            
            &-value {
                .span-text {
                    @include gradient-span($gradient-blueberry-dark, $gradient-blueberry-light);
                }
            }

            
        }
    }

    .two-column {
        &-block {
            &.__reviews {
                .two-column {
                    &-image {
                        &::before {
                            @extend %clip-path-business;
                        }

                        @media (min-width:$lg) {
                            &::before {
                                @include gradient-background($gradient-blueberry-dark, $gradient-blueberry-light);
                            }
                        }

                        img {
                            @extend %clip-path-business;
                        }
                    }
                }
            }
        }

        &-image {
            img {
                @extend %clip-path-business-rotate;
            }
        }
    }

    .videoplay {
        &:hover,
        &:focus {
            @include gradient-background($gradient-blueberry-dark, $gradient-blueberry-light);
        }
    }

    .gform {
        &_wrapper {
            &.gravity-theme {
                .gf_progressbar_wrapper {
                    .gf_progressbar {
                        .gf_progressbar_percentage {
                            @include gradient-progressbar($gradient-blueberry-light, $gradient-blueberry-dark);
                        }
                    }
                }

                .gf_page_steps {
                    .gf_step {
                        &.gf_step_active {
                            .gf_step_number  {
                                border: none;
                                @include gradient-progressbar($gradient-blueberry-light, $gradient-blueberry-dark);
                            }
                        }
                    }
                }

                .ginput_container {
                    &.ginput_container_checkbox {
                        .gchoice {
                            &::after {
                                color: $color-blueberry-500;
                            }
                        
                            &.__checked {
                                &::before {
                                    border-color: $color-blueberry-500;
                                }
                            }

                            &.__focused {
                                &::before {
                                    border-color: darken($color-blueberry-500, 10%);
                                }
                            }
                        }
                    }

                    &.ginput_container_radio {
                        .gchoice {                   
                            label {                       
                                &::after {
                                    background-color: $color-blueberry-500;
                                }
                            }

                            &.__checked {
                                label {
                                    &::before {
                                        border-color: $color-blueberry-500;
                                    }
                                }
                            }
                        }
                    }


                    &.ginput_container_multiselect {
                        .select2-container--default .select2-selection--multiple .select2-selection__choice {
                            background: rgba($color-blueberry-900, 0.2);
                        }
                    }

                    &.ginput_container_consent {
                        &::after {
                            color: $color-blueberry-900;
                        }

                        &.__checked {
                            &::before {
                                border-color: $color-blueberry-900;
                            }
                        }
                    }
                }
            }
        }
    }
}