.contact {
    background-color: $color-white;
    border-radius: 40px;
    padding: 40px 0;
    margin-bottom: 60px;

    @media (min-width:$lg) {
        padding: 80px 0;
        margin-bottom: 119px;
    }

    &-locations {
        margin-bottom: 40px;
        
        @media (min-width:$lg) {
            margin-bottom: 60px;
        }
        
        &-list {
            li {
                display: flex;
                position: relative;
                margin-bottom: 8px;
            }
        }

        &-wrap {
            background-color: $color-white;
            border-radius: 16px;
            padding: 20px;
            margin-bottom: 26px;
        }

        a {
            text-decoration: underline;

            &:hover,
            &:focus {
                text-decoration: none;
            }
        }

        .icon {
            margin-right: 10px;
            width: 24px;
        }
    }

    &-form {
        margin-bottom: 40px;
        
        @media (min-width:$lg) {
            margin-bottom: 60px;
        }
    }

    &-map {
        padding-bottom: 40px;
        
        @media (min-width:$lg) {
            padding-bottom: 60px;
        }
    }
}