/* Default body text size*/
$font-size-num: 20;
$font-size: #{$font-size-num}px;

/* Title font */
$title-font: 'freight-sans-condensed-pro-u', Arial, sans-serif;
$title-color: #190633; // color-blackberry-500
$title-regular: 400;
$title-extra-bold: 900;

/* Base font */
$body-family: 'freight-sans-pro', Arial, sans-serif;
$body-color: #000000; // color-black
$body-light: 300;
$body-regular: 400;
$body-medium: 500;
$body-semibold: 600;
$body-bold: 700;

/* Devices */
$xs: 575px;
$sm: 576px;
$md: 768px;
$lg: 992px;
$xl: 1200px;
$xxl: 1400px;

$nav-bp: 992px; // match the value in /js/main.js
$nav-width-mobile: 100%;
$nav-width: 308px;

/* Base colors */
$color-black: #000000;
$color-graphite: #454444;
$color-lightgrey: #f5f5f5;
$color-white: #FFFFFF;
$color-clear: transparent;

// Raspberry
$color-raspberry-50: #fcedfe;
$color-raspberry-100: #f5c8fb;
$color-raspberry-200: #f0aef9;
$color-raspberry-300: #e989f6;
$color-raspberry-400: #e572f4;
$color-raspberry-500: #de4ff1;
$color-raspberry-600: #ca48db;
$color-raspberry-700: #9e38ab;
$color-raspberry-800: #7a2b85;
$color-raspberry-900: #5d2165;

// Blackberry
$color-blackberry-50: #e8e6eb;
$color-blackberry-100: #b8b2c0;
$color-blackberry-200: #958ca1;
$color-blackberry-300: #655876;
$color-blackberry-400: #47385c;
$color-blackberry-500: #190633;
$color-blackberry-600: #17052e;
$color-blackberry-700: #120424;
$color-blackberry-800: #0e031c;
$color-blackberry-900: #0b0315;

// Blueberry
$color-blueberry-50: #efeefe;
$color-blueberry-100: #cec9fb;
$color-blueberry-200: #b7aff9;
$color-blueberry-300: #968af6;
$color-blueberry-400: #8173f5;
$color-blueberry-500: #6250f2;
$color-blueberry-600: #5949dc;
$color-blueberry-700: #4639ac;
$color-blueberry-800: #362c85;
$color-blueberry-900: #292266;

// Juniper
$color-juniper-50: #eefdfe;
$color-juniper-100: #c9fafb;
$color-juniper-200: #aff8f9;
$color-juniper-300: #8af4f6;
$color-juniper-400: #73f2f5;
$color-juniper-500: #50eff2;
$color-juniper-600: #49d9dc;
$color-juniper-700: #39AAAC;
$color-juniper-800: #2c8385;
$color-juniper-900: #226466;

// Orange
$color-orange-50: #fff5e7;
$color-orange-100: #ffe0b5;
$color-orange-200: #ffd191;
$color-orange-300: #ffbc5e;
$color-orange-400: #ffaf3f;
$color-orange-500: #ff9b0f;
$color-orange-600: #e88d0e;
$color-orange-700: #b56e0b;
$color-orange-800: #8c5508;
$color-orange-900: #6b4106;

$color-error-40: #F06799;
$color-error-80: #8a0133;

/* Gradients */
$gradient-raspberry-dark: #FF2CDE;
$gradient-raspberry-light: #FF72E9;

$gradient-blueberry-dark: #6250F2;
$gradient-blueberry-light: #8272F5;

/* Components */
$section-background: #FDFAFF;
$dark-background: #161616; // matches title

/* Form colors */
$form-bg: $color-blackberry-50;
$form-border: #EAE9E9;
$form-border-focus: $color-raspberry-800;
$form-label: $body-color;
$form-text: $body-color;
$form-helper: lighten($body-color, 20%);
$form-placeholder: $color-blackberry-500;

$form-error-bg: $color-error-40;
$form-error-border: $color-error-80;
$form-error-text: $color-error-80;
$form-success-bg: #dff4e3;
$form-success-border: #207900;
$form-success-text: #207900;

/* Alert colors */
$alert-info: #f2f0f0;
$alert-success: #19aad1;
$alert-error: #fb3853;
$alert-warning: #FFC80C;

/* Box shadow */
$box-shadow-xxsmall: 0px 1px 2px 0px #0000000D;
$box-shadow-xsmall: 0px 1px 2px 0px #0000000F, 0px 1px 3px 0px #0000001A;
$box-shadow-small: 0px 2px 4px -2px #0000000F, 0px 4px 8px -2px #0000001A;
$box-shadow-medium: 0px 4px 6px -2px #00000008, 0px 12px 16px -4px #00000014;
$box-shadow-large: 0px 8px 8px -4px #00000008, 0px 20px 24px -4px #00000014;
$box-shadow-xlarge: 0px 24px 48px -12px #0000002E;