/* Helper classes */
// Screenreader only content
.visually-hidden {
    position: absolute;
    left: -999999px;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    border: 0;
    transition: none;

    &-focusable {
        border: 0;
        clip: rect(0, 0, 0, 0);
        height: 1px;
        left: -999999px;
        margin: -1px;
        overflow: hidden;
        padding: 0;
        position: absolute;
        transition: none;
        width: 1px;

        &:focus {
            border: auto;
            clip: auto;
            height: auto;
            left: auto;
            margin: auto;
            overflow: auto;
            padding: auto;
            position: relative;
            width: auto;

            &.skiptomain {
                background: $color-white;
                clip: auto;
                color: black;
                height: auto;
                left: 5px;
                margin: 0;
                position: absolute;
                top: 5px;
                width: auto;
                z-index: 1;
            }
        }
    }
}

/* Helper extenders */
%icon {
    display: inline-block;
    text-transform: none;
    vertical-align: middle;
    font-family: icomoon;
    font-size: 26px;
    color: $color-blackberry-500;
    font-weight: normal;
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    speak: none;
    transition: all 0.3s;
}

[class^="icon-"],
[class*=" icon-"],
.icon {
    @extend %icon;
}

%overlay {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: $color-black;
    opacity: 0.4;
    transition: all 0.3s;
}

%bgcover {
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
}

%objectfitcontain {
    width: 100%;
    height: 100%;
    object-fit: contain;
    object-position: center;
}

%objectfitcover {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
}

%section-start-background {
    background-image: url('/images/background/background-top.svg');
    background-position: top left;
    background-repeat: no-repeat;
    background-size: 40%;
}

%section-end-background {
    background-image: url('/images/background/background-bottom.svg');
    background-position: bottom right;
    background-repeat: no-repeat;
    background-size: 40%;
}

%text-gradient-raspberry {
    background: linear-gradient(180deg, $gradient-raspberry-dark 0%, $gradient-raspberry-light 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

%text-gradient-blueberry {
    background: linear-gradient(180deg, $gradient-blueberry-dark 0%, $gradient-blueberry-light 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

%text-gradient-orange {
    background: linear-gradient(90deg, $color-orange-500 0%, $color-orange-300 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

%inline-button-hover {
    .button .button-text::after {
        width: 100%;
    }
}

%dark-button-hover {
    @include gradient-background($gradient-raspberry-dark, $gradient-raspberry-light);
    color: $color-blackberry-500;
    transform-origin: top left;
    transform: rotate(1deg);
}

