.tabpanel {
    padding: 40px 0;

    @media (min-width:$md) {
        padding: 73px 0 90px 0;
    }

    &-head {
        background-color: $color-blackberry-400;
        border-radius: 16px;
        padding: 8px;
        display: flex;
        justify-content: flex-start;
        gap: 16px;
        overflow-x: auto;
        margin-right: -32px;

        @media (min-width:$sm) {
            margin-right: 0;
        }
    }

    &-tab {
        background-color: $color-blackberry-500;
        border-radius: 8px;
        padding: 8px 16px;
        display: inline-block;
        text-align: center;
        color: $color-white;
        font-size: convertRem(20);
        font-weight: $body-semibold;
        cursor: pointer;
        white-space: nowrap;
        position: relative;
        transition: 0.3s ease all;

        &::before {
            content: '';
            width: 8px;
            height: 8px;
            background-color: $color-white;
            display: inline-block;
            border-radius: 50%;
            left: 0;
            transform: translate(16px, 10px);
            position: absolute;
            opacity: 0;
            transition: opacity 0.4s ease;

        }

        &:hover,
        &:focus {
            padding-left: 28px;

            &::before {
                opacity: 1;
            }
        }

        &:last-child {
            margin-right: 14px;

            @media (min-width:$md) {
                margin-right: 0;
            }
        }

        &.__active {
            background: $color-white;
            color: $title-color;

            &::before {
                background-color: $title-color;
            }
        }
    }

    &-content {
        .usercontent {
            margin-bottom: 24px;

            h2 {
                margin-bottom: 32px;

                @media (min-width:$md) {
                    margin-bottom: 24px;
                }
            }
        }

        .button-wrap {
            margin-bottom: 32px;
            margin-top: 32px;

            @media (min-width:$lg) {
                margin-bottom: 0;
                margin-top: 24px;
            }
        }
    }

    &-panel {
        font-size: convertRem(18);
        padding: 32px 0 0 0;
        color: #333;
        display: none;
        z-index: 1;
        position: relative;

        @media (min-width:$md) {
            padding: 61px 0 0 0;
        }

        &.__active {
            display: block;
        }
    }
}

.tabsblock {
    overflow: hidden;
    position: relative;

    @media (min-width:$lg) {
        .tabsblock-background {
            position: absolute;
            left: -35%;
            top: 474px;

            svg {
                width: 1317.028px;
                height: 1335.822px;
                transform: rotate(26.033deg);
                flex-shrink: 0;
                
                path {
                    fill: $color-blackberry-700;
                }
            }
        }
    }

    &.__background-dark {
        @media (min-width:$lg) {
            min-height: 620px;
        }
    }
}