.footer {
    background: $color-blackberry-700;
    padding: 20px 0;
    text-align: center;
    color: $color-white;

    @media (min-width: $sm) {
        padding: 42px 0;
        text-align: left;
    }

    &.__upper {
        padding-top: 57px;

        @media (min-width:$sm) {
            padding: 48px 0 0 0;
        }

        .footer-nav {
            border-bottom: 1px solid $color-blackberry-400;
            padding-bottom: 53px;

            @media (min-width:$sm) {
                padding-bottom: 30px;
            }

            ul {
                align-items: center;
                flex-wrap: wrap;

                @media (min-width:$sm) {
                    align-items: flex-start;
                }

                li {
                    &:not(:last-child) {
                        margin-right: 14px;

                        @media (min-width:$sm) {
                            margin-right: 34px;
                        }
                    }

                    a {
                        background: rgba($color-blackberry-400, 0);
                        border-radius: 30px;
                        display: inline;
                        padding: 4px 8px;
                        transition: 0.3s ease all;
                        font-weight: $body-semibold;
                        
                        &::after {
                            content: none;
                        }

                        &:hover,
                        &:focus {
                            background: rgba($color-blackberry-400, 1);
                            color: $color-white;
                        }
                    }
                }
            }
        }
    }

    &.__middle {
        padding: 23px 0;

        @media (min-width:$sm) {
            padding: 96px 0 22px 0;
        }

        .footer-contact {
            flex: 0 0 100%;

            @media (min-width:$sm) {
                margin-right: 76px;
                flex: 0 1 0%;
            }

            ul {
                flex-direction: column;
                margin-bottom: 53px;

                @media (min-width:$sm) {
                    margin-bottom: 20px;
                }

                li {
                    &:first-child {
                        margin-bottom: 31px;
                    }

                    &:nth-child(2) {
                        margin-bottom: 37px;
                    }

                    a {
                        display: flex;
                        align-items: center;
                        flex-direction: column;
                        margin-bottom: 30px;

                        @media (min-width:$sm) {
                            flex-direction: row;
                            margin-bottom: 0;
                        }

                        &:hover,
                        &:focus {
                            .link-text::after {
                                width: 100%;
                            }
                        }
                    }

                    .icon {
                        margin-bottom: 16px;

                        @media (min-width:$sm) {
                            margin: 0 56px 0 0;
                        }

                        &::before {
                            color: $color-white;
                            font-size: convertRem(18);
                        }
                    }

                    .link-text {
                        position: relative;

                        &::after {
                            content: '';
                            position: absolute;
                            background-color: $color-white;
                            left: 0;
                            bottom: -2px;
                            height: 1px;
                            width: 0%;
                            transition: 0.3s ease all;
                        }
                    }
                }
            }

            img {
                width: 76px;
                margin-bottom: 22px;
                

                @media (min-width:$sm) {
                    margin: 0 32px 0 0;
                }
            }
        }

        .footer-quicklinks {
            ul {
                display: grid;
                grid-template-columns: repeat(3, auto);
                gap: 30px 32px;
                width: fit-content;
            }
        }
    }

    &.__lower {
        @media (min-width:$sm) {
            padding: 40px;
        }

        .footer-col.footer-copyright {
            font-size: convertRem(16);
            flex-direction: column;

            span {
                color: $color-blackberry-100;
                margin-right: 3px;
            }

            a {
                width: fit-content;
            }
        }
    }

    &-logo {
        display: flex;
        justify-content: center;
        margin-top: 53px;

        @media (min-width:$sm) {
            justify-content: flex-end;
            margin-top: 7px;
        }

        img {
            width: 151px;

            @media (min-width:$sm) {
                width: 297px;
            }
        }
    }

    &-nav,
    &-col {
        ul {
            display: flex;
        }

        a {
            display: inline-flex;
            transition: all 0.3s;
            position: relative;

            &::after {
                content: '';
                position: absolute;
                background-color: $color-white;
                left: 0;
                bottom: -2px;
                height: 1px;
                width: 0%;
                transition: 0.3s ease all;
            }

            &:hover,
            &:focus {
                color: $color-white;
                &::after {
                    width: 100%;
                }
            }
        }
    }

    &-title {
        color: $color-white;
        margin-bottom: 30px;
    }
}

footer {
    border-radius: 32px 32px 0 0;
    overflow: hidden;
    position: relative;
    z-index: 3;
    
    @media (min-width:$sm) {
        border-radius: 0;
    }
}