.button {
    background: $color-blackberry-500;
    color: $color-white;
    display: inline-block;
    padding: 12px 24px;
    border: none;
    border-radius: 16px;
    text-align: center;
    text-decoration: none;
    font-size: convertRem(20);
    font-weight: $body-semibold;
    text-transform: none;
    cursor: pointer;
    position: relative;
    transition: all 0.3s;
    width: fit-content;

    &.__dark {
        background: $color-blackberry-500;
        color: $color-white;

        &:hover,
        &:focus {
            @include gradient-background($gradient-raspberry-dark, $gradient-raspberry-light);
            color: $color-blackberry-500;
            transform-origin: top left;
            transform: rotate(1deg);
        }
    }
    
    &.__light {
        background: $color-white;
        color: $color-blackberry-500;

        &:hover,
        &:focus {
            @include gradient-background($gradient-raspberry-dark, $gradient-raspberry-light);
            color: $color-blackberry-500;
            transform-origin: top left;
            transform: rotate(1deg);
        }
    }

    &.__gradient-dark {
        @include gradient-background($gradient-raspberry-dark, $gradient-raspberry-light);
        color: $color-blackberry-500;

        &:hover,
        &:focus {
            background: $color-blackberry-500 !important;
            color: $color-white;
            transform-origin: top right;
            transform: rotate(-1deg);
        }
    }

    &.__gradient-light {
        @include gradient-background($gradient-raspberry-dark, $gradient-raspberry-light);
        color: $color-blackberry-500;

        &:hover,
        &:focus {
            background: $color-white !important;
            color: $color-blackberry-500;
            transform-origin: top right;
            transform: rotate(-1deg);
        }
    }

    

    &.__icon,
    &.__icononly {
        .icon {
            display: flex;
            align-items: center;

            &::after {
                content: '\e91a';
                color: $color-white;
                font-size: convertRem(24);
                transition: 0.3s ease all;
            }
        }
    }

    &.__icon {
        color: $color-white;
        display: inline-flex;
        padding: 11.5px 22px;

        &:hover,
        &:focus,
        &:active {
            @include gradient-background($gradient-raspberry-dark, $gradient-raspberry-light);
        }


        .icon {
            margin-right: 16px;
        }

        &.__outline {
            background: $color-clear;
            color: $color-blackberry-500;
            padding: 9.5px 22px;

            .icon::after {
                content: '\e919';
                color: $color-blackberry-500;
            }

            &:hover,
            &:focus {
                background: $color-white !important;
                border-color: $color-white;
            }
        }
    }

    &.__icononly {
        padding: 12px;

        &:hover,
        &:focus {
            .icon::after {
                color: $color-blackberry-500
            }
        }

        &.__light {
            .icon::after {
                color: $color-blackberry-500
            }
        }

        &.__download {
            .icon::after {
                content: '\e907';
            }
        }
    }

    &.__inline {
        background: transparent;
        color: $color-blackberry-500;
        padding: 10px 0;
        align-items: center;

        .icon {
            @include gradient-background($gradient-raspberry-dark, $gradient-raspberry-light);
            border-radius: 50%;
            align-items: center;
            justify-content: center;
            width: 16px;
            height: 16px;
            flex: 0 0 16px;

            &::after {
                color: $color-white;
                font-size: convertRem(10);
            }
        }

        .button-text {
            position: relative;

            &::after {
                content: '';
                background-color: $color-blackberry-500;
                height: 2px;
                width: 0;
                position: absolute;
                bottom: 1px;
                left: 0;
                transition: 0.3s ease all;
            }
        }

        &:hover,
        &:focus {
            background: none !important;

            .button-text::after {
                width: 100%;
            }
        }
    }

    &.__outline {
        background: $color-clear;
        border: 2px solid $color-blackberry-500;
        color: $color-blackberry-500;

        &:hover,
        &:focus,
        &:active {
            background-color: $color-white !important;
            border-color: $color-white;
        }
    }

    &-wrap {
        display: flex;
        flex-wrap: wrap;
        gap: 16px;
    }
}

//styling for disabled buttons
a[aria-disabled="true"],
button[disabled] {
    cursor: not-allowed;
    opacity: 0.6;
}

.submit_wrap button,
.form-submit button {
    @extend .button;
}