.two-column {
    display: flex;
    flex-direction: column-reverse;
    justify-content: space-between;

    &.__textonly {
        .two-column-text-wrap {
            &:first-child {
                @media (min-width:$lg) {
                    padding-right: 30px;
                }
                
                @media (min-width:$xl) {
                    padding-right: 80px;
                }
            }
        }
    }

    &.__alt {
        .two-column-image {
            transform: rotate(-0.248deg);
        }
    }

    &-block {
        border-radius: 24px;

        @media (min-width:$md) {
            border-radius: 40px;
        }
        
        &.__background-dark {
            .two-column-title {
                color: $color-white;
            }
        }

        &.__background-light:not(.__frameless-image) {
            .two-column {
                flex-direction: column;

                @media (min-width:$lg) {
                    flex-direction: row-reverse;
                }
            }
        }

        &.__frameless-image {
            border-radius: 0;
            padding: 64px 0;

            @media (min-width:$lg) {
                border-radius: 40px;
            }

            .two-column {
                &.__alt {
                    @media (min-width:$lg) {
                        .two-column-text {
                            margin-right: 8.33333333%;
                        }
                    }
                }
                &-image {
                    transform: none !important;
                    margin-bottom: 24px;

                    @media (min-width:$lg) {
                        margin-bottom: 0;
                    }

                    img {
                        aspect-ratio: unset;
                        clip-path: none;
                    }
                }

                &-text {
                    padding: 0;
                    text-align: center;

                    @media (min-width:$lg) {
                        text-align: left;
                    }

                    .button-wrap {
                        justify-content: center;
                        margin-top: 24px;
                        
                        @media (min-width:$lg) {
                            justify-content: flex-start;
                            margin-top: 32px;
                        }
                    }
                }

                &-title {
                    margin-bottom: 24px;

                    @media (min-width:$lg) {
                        margin-bottom: 16px;
                    }
                }
            }
        }

        &:not(.__frameless-image) {
            .two-column.__alt {
                .two-column-image {
                    margin: 0 0 40px 0;
                }
            }
        }

        &.__reviews {
            &.__inverted-radius-bottom {
                border-radius: 40px 40px 0 0;
                position: relative;

                &::before {
                    content: "";
                    position: absolute;
                    background-color: transparent;
                    bottom: 0;
                    left: 0;
                    height: 40px;
                    width: 40px;
                    border-top-left-radius: 40px;
                    box-shadow: -18px -20px 0px 17px $color-blackberry-500;
                    z-index: 0;
                    transform: translateY(40px);
                }
            
                &::after {
                    content: "";
                    position: absolute;
                    background-color: transparent;
                    bottom: 0;
                    right: 0;
                    height: 40px;
                    width: 40px;
                    border-top-right-radius: 40px;
                    box-shadow: 18px -20px 0px 17px $color-blackberry-500;
                    z-index: 0;
                    bottom: 0;
                    transform: translateY(40px);
                }
            }

            &.__in-view {
                .two-column-image {
                    transform: rotate(10deg);

                    &::before {
                        transform: rotate(-20deg);
                    }
                }
            }

            .two-column {
                padding: 32px 0 23px 0;
                align-items: center;
                flex-direction: column;

                @media (min-width:$sm) {
                    padding: 66px 0 62px 0;
                }

                @media (min-width:$lg) {
                    flex-direction: row;
                    padding: 96px 0 62px 0;
                }

                &-image {
                    transform: none;
                    padding: 0 4px;
                    width: fit-content;
                    transition: 0.5s ease all;

                    &::before {
                        @extend %clip-path-home;
                    }

                    @media (min-width:$lg) {
                        max-width: unset;
                        margin: 0 60px 0 0;
                        padding: 0;
                        position: relative;

                        &::before {
                            content: '';
                            @include gradient-background($gradient-raspberry-dark, $gradient-raspberry-light);
                            position: absolute;
                            max-width: 480px;
                            max-height: 457.914px;
                            width: 100%;
                            height: 100%;
                            @include aspect(0.7, 1);
                            transition: 0.5s ease all;
                        }
                    }

                    @media (min-width:$xl) {
                        margin: 0 0 0 12%;
                    }

                    img {
                        @extend %clip-path-home;
                        max-width: 480px;
                        max-height: 457.914px;

                        
                    }
                }

                &-text {
                    padding: 32px 9px 0 9px;

                    @media (min-width:$lg) {
                        padding: 0 0 0 47px;
                    }
                }
            }
            .two-column-review {
                &-rating {
                    margin-bottom: 24px;

                    .icon-star {
                        &::before {
                            @extend %text-gradient-orange;
                            font-size: convertRem(37);
                        }
                    }
                }
                &-title {
                    color: $color-white;
                    margin-bottom: 24px;
                    letter-spacing: -1.2px;
                }

                &-text {
                    p {
                        color: $color-white;
                        margin-bottom: 24px;
                    }
                }

                &-author {
                    color: $color-white;
                    letter-spacing: -0.72px;

                    &::before {
                        content: '-';
                        font-family: $title-font;
                        color: $color-white;
                        margin-right: 2px;
                    }
                }

                &-cta {
                    margin-top: 33px;
                }
            }
        }
    }

    &-text {
        padding: 48px 0;

        @media (min-width:$lg) {
            padding: 96px 0;
        }
    }

    &-title {
        font-size: convertRem(56);
        margin-bottom: 24px;

        @media (min-width:$lg) {
            font-size: convertRem(88);
            line-height: 0.69;
            letter-spacing: normal;
        }
    }

    &-image {
        position: relative;        
        transform: rotate(-0.248deg);
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 12px;
        max-height: 677px;

        @media (min-width:$lg) {
            justify-content: flex-start;
            transform: rotate(9.752deg) translateY(-25px);
            margin-top: 0;
            max-width: 45%;
        }

        @media (min-width:$xl) {
            margin-top: -25px;
            margin-bottom: -25px;
        }

        img {
            @include aspect(1.02, 1);
            @extend %clip-path-home-rotate;
            
            @media (min-width: $lg) {
                height: 100%;
                margin-left: auto;
            }
        }
    }

    @media (min-width: $lg) {
        flex-direction: row;

        &.__alt {
            .two-column-image {
                margin-bottom: -25px;
                transform: rotate(-20.248deg) translateY(-25px);

                img {
                    margin-left: unset;
                    margin-right: auto;
                }
            }
        }
    }

    @media (min-width: $xl) {
        &.__alt {
            flex-direction: row-reverse;
        }
    }

    .button-wrap {
        margin-top: 24px;
    }
}
